import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs';
import {UserDetailInterface} from '../interface/user-detail.interface';

@Injectable({
  providedIn: 'root'
})
export class TopNavBarService {
  private userDetails: UserDetailInterface;
  public userDetailsSubject = new BehaviorSubject<UserDetailInterface>(this.userDetails);
  public userDetailsObservable =  this.userDetailsSubject.asObservable();
  constructor() { }
  updateUserDetails(userDetails: UserDetailInterface) {
    this.userDetailsSubject.next(userDetails);
  }
}
