import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
    selector: 'app-menu',
    template: '<app-list [currentAppCode]="currentAppCode" *ngIf="currentAppCode"></app-list>',
    styleUrls: ['./app-switcher.component.scss']
})
export class AppMenuComponent implements OnInit {
    currentAppCode: any;

    constructor(public dialogRef: MatDialogRef<AppMenuComponent>, @Inject(MAT_DIALOG_DATA) public data: any) {
        this.currentAppCode = data.currentAppCode;
    }

    ngOnInit() {}
}
