import {FlatTreeControl} from '@angular/cdk/tree';
import {Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, AfterViewInit} from '@angular/core';
import {MatTreeFlatDataSource, MatTreeFlattener} from '@angular/material/tree';
import {urls} from '../constants/urls';
import {HttpParams} from '@angular/common/http';
import { ITopologyNodes, TopologytreeService} from '../services/topologytree.service';
import {DragDropItemFlatNode, DragDropItemNode, TreeDragDropItemDataBase} from './topology-dragdrop-database';
import {TopologyService} from '../services/topology.service';
import {CageService} from '../services/cage.service';
import {CasinomanagerService} from '../services/casinomanager.service';
import {Router} from '@angular/router';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import {FormBuilder, FormControl, Validators} from '@angular/forms';
import {TranslateService} from '@ngx-translate/core';
import {dialogSize} from '../constants/constants';
import {ConfirmDeleteComponent} from './confirm-delete/confirm-delete.component';
import {JwtHelperService} from '@auth0/angular-jwt';


@Component({
    selector: 'app-toplogy-tree-dragdrop',
    templateUrl: './topology-tree-drag-drop.component.html',
    styleUrls: ['./topology-tree-drag-drop.component.scss'],
    providers: [TreeDragDropItemDataBase]
})
export class TopologyTreeDragDropComponent implements OnInit, OnDestroy, AfterViewInit {
    /* Map from flat node to nested node. This helps us finding the nested node to be modified */
    flatNodeMap = new Map<DragDropItemFlatNode, DragDropItemNode>();
    /* Map from nested node to flattened node. This helps us to keep the same object for selection */
    nestedNodeMap = new Map<DragDropItemNode, DragDropItemFlatNode>();
    treeControl: FlatTreeControl<DragDropItemFlatNode>;
    treeFlattener: MatTreeFlattener<DragDropItemNode, DragDropItemFlatNode>;
    /* Drag and drop */
    dragNode: any;
    dragNodeExpandOverWaitTimeMs = 300;
    dragNodeExpandOverNode: any;
    dragNodeExpandOverTime: number;
    dragNodeExpandOverArea: string;
    @ViewChild('emptyItem') emptyItem: ElementRef;
    @Input() arrTrees: Array<any>;
    topologyTreeConfig = urls.topologyTree;
    assignedDataSource: MatTreeFlatDataSource<DragDropItemNode, DragDropItemFlatNode>;
    unassignedDataSource: MatTreeFlatDataSource<DragDropItemNode, DragDropItemFlatNode>;
    accessGroupDataSource: MatTreeFlatDataSource<DragDropItemNode, DragDropItemFlatNode>;
    virtualGroupDataSource: MatTreeFlatDataSource<DragDropItemNode, DragDropItemFlatNode>;
    arrNestedDataSource = [];
    accessGroupNodesLength: number;
    assingedDataNodes: any;
    nodeNames: any;
    searchText: string;
    nodeUpdated;
    isEditable = false;
    nodeToEdit: string;
    changedName: string;
    currentClickedNode: any;
    isGroupNameInvalid = false;
    selectedNode: string;
    // permissionArray;
    // isSuperuser = false;
    @Output() addNode = new EventEmitter();
    @Input() permissionToAccess = false;
    @Input() permissionToEditAG;



    constructor(
        private database: TreeDragDropItemDataBase,
        private _router: Router,
        private _topologyTreeService: TopologytreeService,
        private _topologyService: TopologyService,
        private _cageService: CageService,
        private _casinoManagerService: CasinomanagerService,
        public snackBar: MatSnackBar,
        public dialog: MatDialog,
        public fb: FormBuilder,
        private jwtHelper: JwtHelperService,
        private translate: TranslateService) {
        this.treeFlattener = new MatTreeFlattener(this.transformer, this.getLevel, this.isExpandable, this.getChildren);
        this.treeControl = new FlatTreeControl<DragDropItemFlatNode>(this.getLevel, this.isExpandable);
        this.handleSubscribers();
        this.addFormValidations();
    }
    public accessGroupForm = this.fb.group({
    });
    public accessGroupEditForm = this.fb.group({
    });

    ngAfterViewInit() {
        this.treeControl.expandAll();
    }

    addFormValidations() {
       this.accessGroupForm.addControl('groupName', new FormControl('', [Validators.required, Validators.maxLength(20)]));
       this.accessGroupEditForm.addControl('groupParentName', new FormControl('', [Validators.required, Validators.maxLength(20)]));
    }



    ngOnInit() {
        this.onitFunction();
        this.treeControl.expandAll();
    }
    onitFunction() {
        this._topologyService.nodeUpdated.subscribe((data) => {
            if (data.msg === 'updateChangeLabel') {
                this.updateTopologyNodes(data.res[0]);
                this.updateNodeNames(data.res[0]);
                this._getAssignedNodes();
            }
        });
        if (!this._topologyService.topologyNodes) {
            this.assignTreeObject();
            this._setUnassignedNodes();
            this._setAccessGroupNodes();
            this._getAssignedNodes();
            this._createDataSources();
            this.databaseSubscriber();
        } else {
            this.assignTreeObject();
            this.setAssignedNodes(this._topologyService.topologyNodes);
            this.createAccessGroupNodes(this._topologyService.accessGroupNodes);
            this.database.dataChangeUnassigned.next(this._topologyService.unAssignedNodes);
            this.nodeNames = this._topologyService.topologyNodeNames;
            this.databaseSubscriber();
            this._createDataSources();
        }
    }

    handleSubscribers() {
        this.nodeUpdated = this._topologyService.nodeUpdated
            .subscribe((res) => {
                if (res.msg === 'createNode') {
                    // this._topologyService.fetchTopologyData();
                    // this._topologyService.initialized.subscribe((rs) => {
                    //     this.onitFunction();
                    // });
                    this.addNodeToTopology(res);
                    // this.currentClickedNode.node.item  = JSON.stringify(this.currentClickedNode.node.item);
                    this.handleClick(this.currentClickedNode.node, this.currentClickedNode.nodeDetails);
                }
                if (res.msg === 'createAccessGroup') {
                    this.addAccessGroupToTopology(res.res);
                }

            });
    }
    addAccessGroupToTopology(res) {
         this._topologyService.accessGroupNodes.push(res[0]);
        this.createAccessGroupNodes(this._topologyService.accessGroupNodes);
    }
    addNodeToTopology(newNode) {
        if (!this._topologyService.topologyNodes[newNode.res[0].parentNodeId]) {
            this._topologyService.topologyNodes[newNode.clickNode.nodeId] = newNode.res;
            }
        // this._topologyService.topologyNodes[newNode.res[0].parentNodeId].push(newNode.res[0]);
        if (!this._topologyService.topologyNodeNames[newNode.res[0].nodeId]) {
            this._topologyService.topologyNodeNames[newNode.res[0].nodeId] = newNode.res[0];
        }
        if (!this._topologyService.topologyNodeNames[newNode.res[0].parentNodeId].children) {
            this._topologyService.topologyNodeNames[newNode.res[0].parentNodeId].children = [];
        }
        this._topologyService.topologyNodeNames[newNode.res[0].parentNodeId].children.push(newNode.res[0]);
        const assignData = this._topologyService.topologyNodes;
        this._topologyService.topologyNodeNames = this._topologyService.createNodeNamesObject(this._topologyService.topologyNodes);
        this._topologyService.siteChildren = this._topologyService.getSiteChildren(this._topologyService.topologyNodeNames);
        this.setAssignedNodes(assignData);
        this.nodeNames = this._topologyService.topologyNodeNames;
        this.databaseSubscriber();
        this._createDataSources();
        // this._getAssignedNodes();
    }
    updateEventObj(obj) {
        this.addNode.emit(obj);
    }

    private assignTreeObject() {
        this.assignedDataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
        this.unassignedDataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
        this.accessGroupDataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
        this.virtualGroupDataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    }

    private databaseSubscriber() {
        this.database.dataChangeAssigned.subscribe(data => {
            this.assignedDataSource.data = [];
            this.assignedDataSource.data = data;
            this.treeControl.expandAll();
        });
        this.database.dataChangeUnassigned.subscribe(data => {
            this.unassignedDataSource.data = [];
            this.unassignedDataSource.data = data;
            this.treeControl.expandAll();
        });
        this.database.dataChangeAG.subscribe(data => {
            this.accessGroupDataSource.data = [];
            this.accessGroupDataSource.data = data;
            this.treeControl.expandAll();
        });
        this.database.dataChangeAG.subscribe(data => {
            this.virtualGroupDataSource.data = data;
            this.treeControl.expandAll();
        });
    }

    /*
     * this function create data source for different group of nodes.
     */
    private _createDataSources() {
        this.arrNestedDataSource['assignednode'] = this.assignedDataSource;
        this.arrNestedDataSource['unassignednode'] = this.unassignedDataSource;
        this.arrNestedDataSource['accessgroup'] = this.accessGroupDataSource;
        this.arrNestedDataSource['virtualgroup'] = this.virtualGroupDataSource;
    }

    /*
     * this function set unassigned data and create the tree for unassigned nodes.
     */
    private _setUnassignedNodes() {
        const unassignedItemsArr = [];
        if (!this._topologyService.unAssignedNodes) {
            this._topologyTreeService.getTopologyNodeUnassigned(this.topologyTreeConfig.unassignedNondeUrl).subscribe(
                (unassignedresponse) => {
                    const childNodes = [];
                    if (unassignedresponse.length > 0) {
                        const params = new HttpParams().set('parentId', unassignedresponse[0].nodeId);
                        this._topologyTreeService.getTopologyNodeByNodeId
                        (this.topologyTreeConfig.topologyNodesUrls, params).subscribe((response) => {
                            for (const item of response) {
                                unassignedItemsArr.push(item);
                                childNodes.push(item.name);
                            }
                            this._topologyService.unassignedNodesArr = unassignedItemsArr;
                            const dataUnassigned = this.database.buildFileTree(childNodes, 0, 'unassignednode');
                            this._topologyService.unAssignedNodes = dataUnassigned;
                            this.database.dataChangeUnassigned.next(dataUnassigned);
                        });
                    }
                }
            );
        } else {
            this.database.dataChangeUnassigned.next(this._topologyService.unAssignedNodes);
        }
    }

    /*
     * this function set Access group data and create the tree for accessgroup nodes.
     */
    private _setAccessGroupNodes() {
        if (!this._topologyService.accessGroupNodes) {
            this._topologyService.initialized.subscribe((value) => {
                if (value === 'nodesInitialized') {
                    const agresponse = this._topologyService.accessGroupNodes;
                    this.createAccessGroupNodes(agresponse);
                }
            });
        } else {
            this.createAccessGroupNodes(this._topologyService.accessGroupNodes);
        }
    }

    private createAccessGroupNodes(agresponse) {
        const topologyIds = new Map<string, string[]>();
        this.accessGroupNodesLength = agresponse.length;
        for (const ag of agresponse) {
            if (ag.topologyNodeIds.length > 0) {
                const childArr = [];
                for (let i = 0, iLen = ag.topologyNodeIds.length; i < iLen; i++) {
                    const childId = ag.topologyNodeIds[i];
                    const childNode = this._topologyService.topologyNodeNames[childId];
                    if (childNode) {
                        childArr.push(childNode.name);
                    }
                }
                topologyIds[ag.groupName] = childArr;
            } else {
                topologyIds[ag.groupName] = [];
            }
        }
        const dataAG = this.database.buildFileTree(topologyIds, 0, 'accessgroup');
        this.database.dataChangeAG.next(dataAG);
    }

    /*
     * this funciton get the response from rest api for assigned nodes.
     */
    private updateTopologyNodes(node) {
        for (const obj in this._topologyService.topologyNodes) {
            if (this._topologyService.topologyNodes.hasOwnProperty(obj)) {
                const topologyData = this._topologyService.topologyNodes;
                if (topologyData[obj].length > 0) {
                    for (let i = 0, len = this._topologyService.topologyNodes[obj].length; i < len; i++) {
                        if (topologyData[obj].nodeId === node.nodeId) {
                            topologyData[obj].name = node.name;
                            topologyData[obj].shortName = node.shortName;
                            topologyData[obj].description = node.description;
                            if (node.type === 1000) {
                                topologyData[obj].host = node.host;
                            }
                        }
                    }
                }
            }
        }
    }

    private updateNodeNames(node) {
        for (const key in this._topologyService.topologyNodeNames) {
            if (parseInt(key, 10) === node.nodeId) {
                this._topologyService.topologyNodeNames[key].name = node.name;
                this._topologyService.topologyNodeNames[key].shortName = node.shortName;
                this._topologyService.topologyNodeNames[key].description = node.description;
                if (node.type === 1000) {
                    this._topologyService.topologyNodeNames[key].host = node.host;
                }
            }
        }
    }

    private _getAssignedNodes(isForSearch?: boolean, newData?: any) {
        if (isForSearch) {
            this.setAssignedNodes(newData, true);
        } else {
            this._topologyService.initialized.subscribe((value) => {
                if (value === 'nodesInitialized') {
                    this.nodeNames = this._topologyService.topologyNodeNames;
                    this.setAssignedNodes(this._topologyService.topologyNodes);
                }
            });
        }
    }

    private setAssignedNodes(assigndata, isForSearch?: boolean) {
        const assignedNodesKeys = Object.keys(assigndata);
        let treeData;
        if (isForSearch && assignedNodesKeys.length > 0) {
            const firstKey = Object.keys(assigndata)[0];
            treeData = this._getflatDataToNested(assigndata, parseInt(firstKey, 10));
            this.setTreeData(treeData, assigndata);
        } else if (isForSearch && assignedNodesKeys.length === 0) {
            treeData = {};
            this.setTreeData(treeData, assigndata);
        } else {
            treeData = this._getflatDataToNested(assigndata);
            this.setTreeData(treeData, assigndata);
        }
    }

    private setTreeData(treeData, assigndata) {
        const topologyParentAssignedGroup = new Map<string, string[]>();
        for (let i = 0; i < treeData.length; i++) {
            topologyParentAssignedGroup[treeData[i].nodeId] = this._getAssignedNestedNodes(treeData[i].children);
        }
        const dataAssignedNodes = this.database.buildFileTree(topologyParentAssignedGroup, 0, 'assignednode');
        this.database.dataChangeAssigned.next(dataAssignedNodes);
        this.assingedDataNodes = assigndata;
    }

    /*
     * this function return array of nested item.
     * param arr
     * private
     */
    private _getAssignedNestedNodes(arr: ITopologyNodes []): Array<any> {
        const arrNodes = [];
        if (arr) {
            for (let i = 0; i < arr.length; i++) {
                if (arr[i] && arr[i].children && arr[i].children.length > 0) {
                    arrNodes[arr[i]['nodeId']] = this._getAssignedNestedNodes(arr[i].children);
                } else {
                    arrNodes.push(arr[i]['nodeId']);
                }
            }
        }
        return arrNodes;
    }

    /*
     * this function build the nested objects from flat object.
     * param arr
     * param parentid
     */
    private _getflatDataToNested(arr: Array<any>, parentId?: number) {
        let output = [];
        const nodeIdsArr = Object.keys(arr);
        const len = nodeIdsArr.length;
        const lastKey = parseInt(nodeIdsArr[len - 1], 10);
        if (!parentId) {
            output.push(arr[0][0]);
            output = this.setParentChildren(output, arr, nodeIdsArr);
        } else {
            const nodesDetail = this._topologyService.topologyNodeNames;
            output.push(nodesDetail[parentId]);
            delete output[0].children;
            output[0].children = arr[parentId];
        }
        if (output[0].children && (output[0].children).length > 0) {
            this.setChildrenOfChildren(output[0], nodeIdsArr, arr, lastKey);
        }
        return output;
    }

    private setChildrenOfChildren(nodeObj, nodeIdsArr, arr, lastKey) {  // recursive function for fetching children of children
        for (const i in nodeObj.children) {
            if (nodeObj.children.hasOwnProperty(i) && !(nodeObj.children[i] > lastKey) &&
                this.checkIfChildren(nodeIdsArr, nodeObj.children[i].nodeId)) {
                nodeObj.children[i].children = arr[nodeObj.children[i].nodeId];
                this.setChildrenOfChildren(nodeObj.children[i], nodeIdsArr, arr, lastKey);
            }
        }
    }

    private setParentChildren(output, arr, nodeIdsArr) {  // set children of parent
        if (this.checkIfChildren(nodeIdsArr, output[0].nodeId)) {
            output[0].children = arr[output[0].nodeId];
        }
        return output;
    }

    private checkIfChildren(nodesArr, nodeId) {  // check if node has children
        return (nodesArr.indexOf(nodeId.toString()) > -1);
    }


    getLevel = (node: DragDropItemFlatNode) => node.level;

    isExpandable = (node: DragDropItemFlatNode) => node.expandable;

    getChildren = (node: DragDropItemNode): DragDropItemNode[] => node.children;

    hasChild = (_: number, _nodeData: DragDropItemFlatNode) => _nodeData.expandable;

    hasNoContent = (_: number, _nodeData: DragDropItemFlatNode) => _nodeData.item === '';

    /*
     * Transformer to convert nested node to flat node. Record the nodes in maps for later use.
     */
    transformer = (node: DragDropItemNode, level: number) => {
        if (node.dataSourceName === 'accessgroup' && node.children && node.children.length > 0) {
            for (let i = 0, iLen = node.children.length; i < iLen; i++) {
                node.children[i].parentName = node.item;
            }
        }
        const existingNode = this.nestedNodeMap.get(node);
        const flatNode = existingNode && existingNode.item === node.item
            ? existingNode
            : new DragDropItemFlatNode();
        flatNode.item = node.item;
        flatNode.level = level;
        flatNode.expandable = (node.children && node.children.length > 0);
        flatNode.dataSourceName = node.dataSourceName;
        if (flatNode.dataSourceName === 'accessgroup' && flatNode.level === 1) {
            flatNode.parentName = node.parentName;
        }
        this.flatNodeMap.set(flatNode, node);
        this.nestedNodeMap.set(node, flatNode);
        return flatNode;
    };

    handleDragStart(event, node) {
        if (!this.permissionToAccess) {
            return false;
        }
        event.dataTransfer.setData('text/plan', node.item);
        this.emptyItem.nativeElement.text = node.item;
        event.dataTransfer.setDragImage(this.emptyItem.nativeElement, 0, 0);
        this.dragNode = node;
        this.treeControl.collapse(node);
    }

    handleDragOver(event, node) {
        event.preventDefault();
        // Handle node expand
        if (node === this.dragNodeExpandOverNode) {
            if (this.dragNode !== node && !this.treeControl.isExpanded(node)) {
                if ((new Date().getTime() - this.dragNodeExpandOverTime) > this.dragNodeExpandOverWaitTimeMs) {
                    this.treeControl.expand(node);
                }
            }
        } else {
            this.dragNodeExpandOverNode = node;
            this.dragNodeExpandOverTime = new Date().getTime();
        }

        // Handle drag area
        const percentageX = event.offsetX / event.target.clientWidth;
        const percentageY = event.offsetY / event.target.clientHeight;
        if (percentageY < 0.25) {
            this.dragNodeExpandOverArea = 'above';
        } else if (percentageY > 0.75) {
            this.dragNodeExpandOverArea = 'below';
        } else {
            this.dragNodeExpandOverArea = 'center';
        }
    }

    // handleDrop: called when a node is dropped , this is called when node of any type is dropped
    handleDrop(event, node) {
        if (!this.permissionToAccess) {
            return false;
        }
        const draggedNodeId = parseInt(this.flatNodeMap.get(this.dragNode).item, 10);
        const nodeDetail = this._topologyService.topologyNodeNames[draggedNodeId];
        if (this.dragNode.dataSourceName === 'assignednode' && this.flatNodeMap.get(node).dataSourceName === 'assignednode') {
            if (nodeDetail && nodeDetail.hasOwnProperty('parentNodeId')) {
                if (nodeDetail.hasOwnProperty('type') && nodeDetail.type === 1200) {
                    const dragNodeObject = this.dragNode;
                    this._cageService.getLocalGamingDay(draggedNodeId).subscribe( (res) => {
                        if (res !== null && res.hasOwnProperty('successObj')) {
                            const localGamingDay = res.successObj;
                            if (localGamingDay) {
                                this._casinoManagerService.getTopologyStatistics(localGamingDay, draggedNodeId, 1, {})
                                    .subscribe((response) => {
                                    if (response !== null) {
                                        let openTables;
                                        if (response.hasOwnProperty('data') && response.data[0].hasOwnProperty('stats')) {
                                           openTables = response.data[0].stats[0].split('/');
                                        }
                                        if (openTables[0] != null && parseInt(openTables[0], 10) === 1) {
                                            this.snackBar.open(this.translate.instant
                                            ('application.app.CONFIGURATION_LABELS.cannot_move_open_table'), '', {
                                                duration: 3000,
                                                horizontalPosition: 'right',
                                                panelClass: 'snack__warn'
                                            });
                                        } else if (openTables[0] != null && parseInt(openTables[0], 10) === 0) {
                                            this.dragNode = dragNodeObject;
                                            event.preventDefault();
                                            this.dragAssignedToAssigned(node, draggedNodeId, nodeDetail);
                                        }
                                    }
                                });
                            }
                        }
                    });
                } else {
                    event.preventDefault();
                    this.dragAssignedToAssigned(node, draggedNodeId, nodeDetail);
                }
            } else {
                this.snackBar.open(this.translate.instant
                ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.CANNOT_ADD_ROOT_LOCATION'), '', {
                    duration: 3000,
                    horizontalPosition: 'right',
                    panelClass: 'snack__warn'
                });
            }
        } else if (this.dragNode.dataSourceName === 'assignednode' && this.flatNodeMap.get(node).dataSourceName === 'accessgroup') {
            if (!this.isTerminal(draggedNodeId)) {
                if (!this.nodeAlreadyExists(draggedNodeId, node)) {
                    event.preventDefault();
                    this.dragToAccessGroup(node, draggedNodeId);
                } else {
                    this.snackBar.open(this.translate.instant
                    ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.LOCATION_ALREADY_EXISTS'), '', {
                        duration: 3000,
                        horizontalPosition: 'right',
                        panelClass: 'snack__warn'
                    });
                }
            } else {
                this.snackBar.open(this.translate.instant
                ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.CANNOT_ADD_TERMN'), '', {
                    duration: 3000,
                    horizontalPosition: 'right',
                    panelClass: 'snack__warn'
                });
            }
        } else if (this.dragNode.dataSourceName === 'unassignednode' && this.flatNodeMap.get(node).dataSourceName === 'assignednode') {
            event.preventDefault();
            this.dragUnassignedNode(node);
        }
        this.handleDragEnd(event);
    }

    // handleDragEnd: reset all the variables used for handling drag and drop
    handleDragEnd(event) {
        this.dragNode = null;
        this.dragNodeExpandOverNode = null;
        this.dragNodeExpandOverTime = 0;
    }

    // dragToAccessGroup: saves the dragged assigned node to the access group nodes tree structure
    dragToAccessGroup(node, draggedNodeId) {
        if (node !== this.dragNode && !this.isTerminal(draggedNodeId)) {
            const siteChildren = this._topologyService.getSiteChildren(this._topologyService.topologyNodeNames);
            const parentNode = this.flatNodeMap.get(node);
            for (let i = 0, iLen = this._topologyService.accessGroupNodes.length; i < iLen; i++) {
                const accessNode =  this._topologyService.accessGroupNodes[i];
                if (accessNode.groupName === this.flatNodeMap.get(node).item) {
                    this.dragNodeToAccess(siteChildren, accessNode, draggedNodeId, parentNode);
                }
            }
        }
    }

    // dragNodeToAccess: called inside dragToAccessGroup
    dragNodeToAccess(siteChildren, accessNode, draggedNodeId, parentNode) {
        for (const obj in siteChildren) {
            if (siteChildren.hasOwnProperty(obj) && siteChildren[obj].id === accessNode.topologyId) {
                const children = siteChildren[obj].children;
                // const children = this.refineSiteChildren(siteChildren[obj].children, accessNode.topologyId, draggedNodeId);
                if (children.indexOf(draggedNodeId) > -1) {
                    if (this.dragNodeExpandOverArea === 'below' || this.dragNodeExpandOverArea === 'center') {
                        for (let j = 0, jLen = this._topologyService.accessGroupNodes.length; j < jLen; j++) {
                            if (this._topologyService.accessGroupNodes[j].groupName === parentNode.item) {
                                const agNode = this._topologyService.accessGroupNodes[j];
                                const idArr = this._topologyService.accessGroupNodes[j].topologyNodeIds;
                                idArr.push(this.dragNode.item);
                                const keysArr = [];
                                for (let a = 0 , aLen = idArr.length; a < aLen; a++) {
                                    keysArr.push(parseInt(idArr[a], 10));
                                }
                                const accessObj = {
                                    groupName: parentNode.item,
                                    groupType: agNode.groupType,
                                    topologyGroupId: agNode.topologyGroupId,
                                    topologyId: agNode.topologyId,
                                    topologyNodeIds: keysArr,
                                    userId: agNode.userId
                                };
                                this._topologyService.updateAccessGroupNode(agNode.topologyGroupId, accessObj)
                                    .subscribe( (res) => {
                                        if (res[0] && res[0].code === 20001) {
                                            this.snackBar.open('Error Occurred. Pls try again later.', '', {
                                                duration: 3000,
                                                horizontalPosition: 'right',
                                                panelClass: 'snack__error'
                                            });
                                            return;
                                        }
                                        for (let b = 0, bLen = this._topologyService.accessGroupNodes.length;
                                             b < bLen; b++) {
                                            if (this._topologyService.
                                                accessGroupNodes[b].groupName === parentNode.item) {
                                                this._topologyService.
                                                    accessGroupNodes[j].topologyNodeIds = keysArr;
                                                break;
                                            }
                                        }
                                        this.updateAccessGroup();
                                        this.snackBar.open(this.translate.instant
                                            ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.SUCCESSFULLY_DROPPED'),
                                            '', {
                                                duration: 3000,
                                                horizontalPosition: 'right',
                                                panelClass: 'snack__success'
                                            });
                                    });
                            }
                        }
                    }
                } else if (accessNode.topologyId === draggedNodeId) {
                    if (this.dragNodeExpandOverArea === 'below' || this.dragNodeExpandOverArea === 'center') {
                        for (let j = 0, jLen = this._topologyService.accessGroupNodes.length; j < jLen; j++) {
                            if (this._topologyService.accessGroupNodes[j].groupName === parentNode.item) {
                                const agNode = this._topologyService.accessGroupNodes[j];
                                const idArr = this._topologyService.accessGroupNodes[j].topologyNodeIds;
                                idArr.push(this.dragNode.item);
                                const keysArr = [];
                                for (let a = 0 , aLen = idArr.length; a < aLen; a++) {
                                    keysArr.push(parseInt(idArr[a], 10));
                                }
                                const accessObj = {
                                    groupName: parentNode.item,
                                    groupType: agNode.groupType,
                                    topologyGroupId: agNode.topologyGroupId,
                                    topologyId: agNode.topologyId,
                                    topologyNodeIds: keysArr,
                                    userId: agNode.userId
                                };
                                this._topologyService.updateAccessGroupNode(agNode.topologyGroupId, accessObj)
                                    .subscribe( (res) => {
                                        if (res[0] && res[0].code === 20001) {
                                            this.snackBar.open('Error Occurred. Pls try again later.', '', {
                                                duration: 3000,
                                                horizontalPosition: 'right',
                                                panelClass: 'snack__error'
                                            });
                                            return;
                                        }
                                        for (let b = 0, bLen = this._topologyService.accessGroupNodes.length;
                                             b < bLen; b++) {
                                            if (this._topologyService.
                                                accessGroupNodes[b].groupName === parentNode.item) {
                                                this._topologyService.
                                                    accessGroupNodes[j].topologyNodeIds = keysArr;
                                                break;
                                            }
                                        }
                                        this.updateAccessGroup();
                                        this.snackBar.open(this.translate.instant
                                            ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.SUCCESSFULLY_DROPPED'),
                                            '', {
                                                duration: 3000,
                                                horizontalPosition: 'right',
                                                panelClass: 'snack__success'
                                            });
                                    });
                            }
                        }
                    }
                } else {
                    this.snackBar.open(this.translate.instant
                    ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.CANNOT_ADD'), '', {
                        duration: 3000,
                        horizontalPosition: 'right',
                        panelClass: 'snack__warn'
                    });
                }
            }
        }
    }

    // dragAssignedToAssigned: saves the dragged assigned node to the assigned nodes tree structure
    dragAssignedToAssigned(node, draggedNodeId, nodeDetail) {
        if (node !== this.dragNode) {
            const prevParentId = nodeDetail.parentNodeId;
            let childNodeId, parentNodeId, childTypeId, parentTypeId, isActualChild;
            childNodeId = parseInt(this.flatNodeMap.get(this.dragNode).item, 10);
            parentNodeId = parseInt(this.flatNodeMap.get(node).item, 10);
            let parentChildren;
            if (parentNodeId && this._topologyService.topologyNodeNames[parentNodeId]) {
                parentChildren =  this._topologyService.topologyNodeNames[parentNodeId].children;
            }
            childTypeId = this._topologyService.topologyNodeNames[childNodeId].type;
            parentTypeId = this._topologyService.topologyNodeNames[parentNodeId].type;
            if (parentChildren && parentChildren.length > 0) {
                for (let i = 0 , iLen = parentChildren.length; i < iLen; i++) {
                    if (parentChildren[i].nodeId === childNodeId) {
                        isActualChild = true;
                        break;
                    } else {
                        isActualChild = false;
                    }
                }
            }
            let allChildren;
            if (this._topologyService.topologyTypes && parentTypeId && this._topologyService.topologyTypes.get(parentTypeId)) {
                allChildren = this._topologyService.topologyTypes.get(parentTypeId).childTypeIds;
            }
            if ((this.dragNodeExpandOverArea === 'below' || this.dragNodeExpandOverArea === 'center') && !isActualChild) {
                this.dragNodeToAssigned(allChildren, childTypeId, parentNodeId, childNodeId, nodeDetail, prevParentId);
            }
        }
    }

    // dragNodeToAssigned: called inside dragAssignedToAssigned
    dragNodeToAssigned(allChildren, childTypeId, parentNodeId, childNodeId, nodeDetail, prevParentId) {
        if (allChildren && allChildren.length > 0 && allChildren.indexOf(childTypeId) > -1) {
            const objToDrag = {
                description: nodeDetail.description,
                nodeId: childNodeId,
                parentNodeId: parentNodeId,
                type: childTypeId
            };
            this._topologyService.updateTopologyNode(childNodeId, objToDrag).subscribe( (res) => {
                if (res) {
                    if (res[0] && res[0].code === 20001) {
                        this.snackBar.open('Error Occurred. Pls try again later.', '', {
                            duration: 3000,
                            horizontalPosition: 'right',
                            panelClass: 'snack__error'
                        });
                        return;
                    }
                    const response = res;
                    this.addToTopologyNodesArr(response, prevParentId);
                    this.updateAssignedNodes();
                    this.snackBar.open(this.translate.instant
                        ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.SUCCESSFULLY_DROPPED'),
                        '', {
                            duration: 3000,
                            horizontalPosition: 'right',
                            panelClass: 'snack__success'
                        });
                    this.handleClick(this.currentClickedNode.node, this.currentClickedNode.nodeDetails);
                }
            });
        } else {
            const parentName = this._topologyService.topologyNodeNames[parentNodeId].name;
            const childName = this._topologyService.topologyNodeNames[childNodeId].name;
            const label = this.translate.instant('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.CANNOT_ADD')
                + '  ' + childName + '  ' +
                this.translate.instant('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.AS_CHILD') + ' ' + parentName;
            this.snackBar.open(label, '', {
                duration: 3000,
                horizontalPosition: 'right',
                panelClass: 'snack__warn'
            });
        }
    }

    // dragUnassignedNode: saves the dragged unassigned node to the assigned nodes tree structure
    dragUnassignedNode(node) {
        let parentNodeId, parentTypeId, allTypeChildren, unassignedNodeId, unassignedNodeType, unassignedNodeName, unassignedNodeDesc;
        parentNodeId = parseInt(this.flatNodeMap.get(node).item, 10);
        parentTypeId = this._topologyService.topologyNodeNames[parentNodeId].type;
        for (let i = 0, iLen = this._topologyService.unassignedNodesArr.length; i < iLen; i++) {
            if (this.dragNode.item === this._topologyService.unassignedNodesArr[i].name) {
                unassignedNodeId = this._topologyService.unassignedNodesArr[i].nodeId;
                unassignedNodeType = this._topologyService.unassignedNodesArr[i].type;
                unassignedNodeName = this._topologyService.unassignedNodesArr[i].name;
                unassignedNodeDesc = this._topologyService.unassignedNodesArr[i].description;
            }
        }
        if (this._topologyService.topologyTypes && parentTypeId && this._topologyService.topologyTypes.get(parentTypeId)) {
            allTypeChildren = this._topologyService.topologyTypes.get(parentTypeId).childTypeIds;
        }
        if ((this.dragNodeExpandOverArea === 'below' || this.dragNodeExpandOverArea === 'center')) {
           this.dragUnassignedToAssigned(allTypeChildren, unassignedNodeType, unassignedNodeDesc,
               unassignedNodeId, parentNodeId, unassignedNodeName);
        }
    }

    // dragUnassignedToAssigned: called inside dragUnassignedNode
    dragUnassignedToAssigned(allTypeChildren, unassignedNodeType, unassignedNodeDesc, unassignedNodeId, parentNodeId, unassignedNodeName) {
        if (allTypeChildren && allTypeChildren.length > 0 && allTypeChildren.indexOf(unassignedNodeType) > -1) {
            const nodeToDrag = {
                description: unassignedNodeDesc,
                nodeId: unassignedNodeId,
                parentNodeId: parentNodeId,
                type: unassignedNodeType
            };
            this._topologyService.updateTopologyNode(unassignedNodeId, nodeToDrag).subscribe( (res) => {
                if (res) {
                    if (res[0] && res[0].code === 20001) {
                        this.snackBar.open('Error Occurred. Pls try again later.', '', {
                            duration: 3000,
                            horizontalPosition: 'right',
                            panelClass: 'snack__error'
                        });
                        return;
                    }
                    const responseArr = res;
                    for (let j = 0, jLen = this._topologyService.unassignedNodesArr.length; j < jLen; j++) {
                        if (this._topologyService.unassignedNodesArr[j] &&
                            unassignedNodeId === this._topologyService.unassignedNodesArr[j].nodeId) {
                            this._topologyService.unassignedNodesArr.splice(j, 1);
                        }
                    }
                    this.updateUnassignedItems();
                    this.addToTopologyNodesArr(responseArr);
                    this.snackBar.open(this.translate.instant
                        ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.SUCCESSFULLY_DROPPED'),
                        '', {
                            duration: 3000,
                            horizontalPosition: 'right',
                            panelClass: 'snack__success'
                        });
                    if (this.currentClickedNode) {
                        this.handleClick(this.currentClickedNode.node, this.currentClickedNode.nodeDetails);
                    }
                }
            });
        } else {
            const parentName = this._topologyService.topologyNodeNames[parentNodeId].name;
            const label = this.translate.instant('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.CANNOT_ADD')
                + '  ' + unassignedNodeName + '  ' +
                this.translate.instant('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.AS_CHILD') + ' ' + parentName;
            this.snackBar.open(label, '', {
                duration: 3000,
                horizontalPosition: 'right',
                panelClass: 'snack__warn'
            });
        }
    }

    // handleClick: called when a node is clicked within the topology tree of assigned nodes type
    handleClick(node, nodeData?: string) {
        if (nodeData){
            if (nodeData['type'] === 1200){
                this.openTableProperties(nodeData);
                return;
            }
        }
        if (node === 'areas') {
            this._topologyService.areasLabelClicked.next();
        } else {
            this.currentClickedNode = {node: node , nodeDetails: nodeData};
            const typeVal = nodeData['type'];
            if (typeVal !== 1000) {
                const objToSend = {
                    nodeClicked: node,
                    topologyData: this._topologyService.topologyNodes,
                    nodeDetails: this._topologyService.topologyNodeNames
                };
                this.selectedNode = node.item;
                this._topologyService.nodeClicked.next(objToSend);
            } else if (typeVal === 1000) {
                this._topologyService.terminalClicked.next();
            }
        }
    }

    // checkSearchText: checks whether the searched text is available or not
    checkSearchText(event) {
        const searchText = event.target.value;
        this.searchText = searchText;
        const assignedKeys = [];
        for (const key in this._topologyService.topologyNodeNames) {
            if (this._topologyService.topologyNodeNames.hasOwnProperty(key)) {
                const node = this._topologyService.topologyNodeNames[key];
                const str = node.name.toLowerCase();
                if (str.indexOf(searchText.toLowerCase()) > -1) {
                    assignedKeys.push(parseInt(key, 10));
                }
            }
        }
        if (assignedKeys.length > 0) {
            this.filterAssignedNodes(assignedKeys);
        }
    }

    // filterAssignedNodes: searches the text entered only within the assigned nodes
    filterAssignedNodes(keysArr) {
        const nodesData = this._topologyService.topologyNodes;
        let filteredAssignedNodes = {};
        for (const key in nodesData) {
            if (nodesData.hasOwnProperty(key) && nodesData[key].length > 0) {
                for (let j = 0, jLen = nodesData[key].length; j < jLen; j++) {
                    const reqNodeId = nodesData[key][j].nodeId;
                    if (keysArr.indexOf(reqNodeId) > -1) {
                        filteredAssignedNodes[key] = nodesData[key];
                    }
                }
            }
        }
        filteredAssignedNodes = this.rectifyFilteredObject(filteredAssignedNodes, keysArr);
        this._getAssignedNodes(true, filteredAssignedNodes);
    }

    // rectifyFilteredObject: returns only the searched nodes
    rectifyFilteredObject(obj, keys) {
        const data = obj;
        for (const val in data) {
            if (data.hasOwnProperty(val) && data[val].length > 0) {
                const tempArr = [];
                for (let i = 0, iLen = data[val].length; i < iLen; i++) {
                    if (keys.indexOf(data[val][i].nodeId) > -1) {
                        tempArr.push(data[val][i]);
                        if (data[val][i].children && data[val][i].children.length > 0) {
                            const arr = [];
                            for (let j = 0, jLen = data[val][i].children.length; j < jLen; j++) {
                                const child = data[val][i].children[j];
                                if (keys.indexOf(child.nodeId) > -1) {
                                    arr.push(child);
                                }
                            }
                            data[val][i].children = arr;
                        }
                    }
                }
                data[val] = tempArr;
            }
        }
        return data;
    }

    // cancelSearch: reset all the variables when the search is cancelled
    cancelSearch() {
        this.searchText = '';
        const assignData = this._topologyService.topologyNodes;
        this.setAssignedNodes(assignData);
    }

    // openTableProperties: opens the table properties screen of the table clicked from the topology tree
    openTableProperties(clickedTable) {
        const tableId = clickedTable.nodeId;
        this._router.navigate(['/configuration/table-properties/' + tableId]);
    }

    ngOnDestroy() {
        this.nodeUpdated.unsubscribe();
    }

    // delete the selected node of the access group nodes
    deleteAccessGroup(node) {
        const nodeNames = this._topologyService.topologyNodeNames;
        const accessNodes = this._topologyService.accessGroupNodes;
        if (node.level === 0) {
          this.isActiveAccessGroup(node, nodeNames, accessNodes);
        } else if (node.level === 1) {
           if (!this.isLastChild(node, nodeNames, accessNodes)) {
               this.removeAccessGroupChild(node, accessNodes, nodeNames);
           } else {
              this.isChildOfActiveAG(node, nodeNames, accessNodes);
           }
        }
    }

    isLastChild(node, nodeNames, accessNodes) {
       let arrLength;
        for (const i in nodeNames) {
            if (nodeNames.hasOwnProperty(i) && nodeNames[i].name === node.item) {
                for (let j = 0, jLen = accessNodes.length; j < jLen; j++) {
                    const index = accessNodes[j].topologyNodeIds.indexOf(nodeNames[i].nodeId);
                    if (index > -1) {
                        arrLength = accessNodes[j].topologyNodeIds.length;
                    }
                }
            }
        }
        return arrLength === 1;
    }

    isChildOfActiveAG(node, nodeNames, accessNodes) {
       let accessGroupId, response;
       for (const i in nodeNames) {
            if (nodeNames.hasOwnProperty(i) && nodeNames[i].name === node.item) {
                for (let j = 0, jLen = accessNodes.length; j < jLen; j++) {
                    const index = accessNodes[j].topologyNodeIds.indexOf(nodeNames[i].nodeId);
                    if (index > -1) {
                       accessGroupId = accessNodes[j].topologyGroupId;
                    }
                }
            }
       }
        this._topologyService.getAccessGroupUsers(accessGroupId, {}).subscribe((res) => {
            if (res) {
                response = res;
                if (response.totalRecordsReturned > 0) {
                    this.openConfirmDeleteBox(node, accessNodes, nodeNames);
                } else {
                    this.removeAccessGroupChild(node, accessNodes, nodeNames);
                }
            }
        });
    }

    // removeAccessGroup : deletes the access group node
    removeAccessGroup(node, accessNodes) {
        for (let a = 0, aLen = accessNodes.length; a < aLen; a++) {
            if (node.item === accessNodes[a].groupName) {
                const objToDelete = {
                    groupName: accessNodes[a].groupName,
                    groupType: accessNodes[a].groupType,
                    topologyGroupId: accessNodes[a].topologyGroupId,
                    topologyId: accessNodes[a].topologyId,
                    topologyNodeIds: accessNodes[a].topologyNodeIds,
                    userId: accessNodes[a].userId
                };
                this._topologyService.deleteAccessGroupNode(accessNodes[a].topologyGroupId, objToDelete).subscribe( (res) => {
                    if (res && res[0] && res[0].code === 20001) {
                        this.snackBar.open('Error Occurred. Pls try again later.', '', {
                            duration: 3000,
                            horizontalPosition: 'right',
                            panelClass: 'snack__error'
                        });
                        return;
                    }
                    for (let b = 0, bLen = this._topologyService.accessGroupNodes.length; b < bLen; b++) {
                        if (this._topologyService.accessGroupNodes[b].groupName === objToDelete.groupName) {
                            this._topologyService.accessGroupNodes.splice(b, 1);
                            break;
                        }
                    }
                    this.snackBar.open(this.translate.instant
                        ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.AG_REMOVED'),
                        '', {
                            duration: 3000,
                            horizontalPosition: 'right',
                            panelClass: 'snack__success'
                        });
                    this.updateAccessGroup();
                });
            }
        }
    }

    // removeAccessGroupChild : deletes the access group child node
    removeAccessGroupChild(node, accessNodes, nodeNames) {
        for (const i in nodeNames) {
            if (nodeNames.hasOwnProperty(i) && nodeNames[i].name === node.item) {
                for (let j = 0, jLen = accessNodes.length; j < jLen; j++) {
                    if (accessNodes[j].groupName === node.parentName) {
                        const index = accessNodes[j].topologyNodeIds.indexOf(nodeNames[i].nodeId);
                        if ( index > -1) {
                            const idArr = accessNodes[j].topologyNodeIds;
                            idArr.splice(index, 1);
                            const accessNodeObj = {
                                groupName: accessNodes[j].groupName,
                                groupType: accessNodes[j].groupType,
                                topologyGroupId: accessNodes[j].topologyGroupId,
                                topologyId: accessNodes[j].topologyId,
                                topologyNodeIds: idArr,
                                userId: accessNodes[j].userId
                            };
                            this._topologyService.updateAccessGroupNode(accessNodes[j].topologyGroupId, accessNodeObj).subscribe( (res) => {
                                if (res[0] && res[0].code === 20001) {
                                    this.snackBar.open('Error Occurred. Pls try again later.', '', {
                                        duration: 3000,
                                        horizontalPosition: 'right',
                                        panelClass: 'snack__error'
                                    });
                                    return;
                                }
                                for (let c = 0, cLen = this._topologyService.accessGroupNodes.length; c < cLen; c++) {
                                    if (this._topologyService.accessGroupNodes[c].groupName === accessNodeObj.groupName) {
                                        this._topologyService.accessGroupNodes[c].topologyNodeIds = idArr;
                                    }
                                }
                                this.snackBar.open(this.translate.instant
                                    ('application.app.CONFIGURATION_LABELS.CHIPSETS.MESSAGE.LOCATION_REMOVED'),
                                    '', {
                                        duration: 3000,
                                        horizontalPosition: 'right',
                                        panelClass: 'snack__success'
                                    });
                                this.updateAccessGroup();
                            });
                        }
                    }
                }
            }
        }
    }

    isActiveAccessGroup(node, nodeNames, accessNodes) {
        let responseData;
        for (let a = 0, aLen = accessNodes.length; a < aLen; a++) {
            if (node.item === accessNodes[a].groupName) {
                const topologyGroupId = accessNodes[a].topologyGroupId;
                this._topologyService.getAccessGroupUsers(topologyGroupId, {}).subscribe((res) => {
                    if (res) {
                        if (res[0] && res[0].code === 20001) {
                            this.snackBar.open('Error Occurred. Pls try again later.', '', {
                                duration: 3000,
                                horizontalPosition: 'right',
                                panelClass: 'snack__error'
                            });
                            return;
                        }
                       responseData = res;
                       if (responseData.totalRecordsReturned > 0) {
                           this.openConfirmDeleteBox(node, accessNodes, nodeNames);
                       } else {
                           this.removeAccessGroup(node, accessNodes);
                       }
                    }
                });
            }
        }
    }

    // editAccessGroupName : called when access group node name is changed
    editAccessGroupName(node) {
        this.isEditable = true;
        this.nodeToEdit = node.item;
        this.accessGroupForm.value.groupName = '';
        this.accessGroupEditForm.value.groupParentName = '';
    }

    // getNodeName : saves the new name of the node to changedName variable
    getNodeName(value) {
        this.changedName = value;
    }

    // updateAccessGroup: updates the tree structure of access group nodes
    updateAccessGroup() {
        this.createAccessGroupNodes(this._topologyService.accessGroupNodes);
        this.databaseSubscriber();
        this._createDataSources();
    }

    // updateUnassignedItems : updates the tree structure of unassigned nodes available
    updateUnassignedItems() {
        const childNodes = [];
        for (const item of this._topologyService.unassignedNodesArr) {
            childNodes.push(item.name);
        }
        const dataUnassigned = this.database.buildFileTree(childNodes, 0, 'unassignednode');
        this._topologyService.unAssignedNodes = dataUnassigned;
        this.database.dataChangeUnassigned.next(dataUnassigned);
    }

    // updateAssignedNodes : updates the tree structure of assigned nodes
    updateAssignedNodes() {
        this.setAssignedNodes(this._topologyService.topologyNodes);
        this._topologyService.topologyNodeNames = this._topologyService.createNodeNamesObject(this._topologyService.topologyNodes);
        this._topologyService.siteChildren = this._topologyService.getSiteChildren(this._topologyService.topologyNodeNames);
        this._topologyService.nodeDragged.next();
        this.databaseSubscriber();
        this._createDataSources();
    }

    // updateNodeName : updates the name of access group node
    updateNodeName(node, formName) {
        let newValue;
        if (formName === 'accessGroupForm') {
            newValue = this.accessGroupForm.value.groupName;
        } else {
            newValue = this.accessGroupEditForm.value.groupParentName;
        }
        if (newValue && newValue !== this.nodeToEdit) {
            const accessNodes = this._topologyService.accessGroupNodes;
            let prevName;
            for (let a = 0, aLen = accessNodes.length; a < aLen; a++) {
                if (node.item === accessNodes[a].groupName) {
                    prevName = node.item;
                    const accessObj = {
                        groupName: newValue,
                        groupType: accessNodes[a].groupType,
                        topologyGroupId: accessNodes[a].topologyGroupId,
                        topologyId: accessNodes[a].topologyId,
                        topologyNodeIds: accessNodes[a].topologyNodeIds,
                        userId: accessNodes[a].userId
                    };
                    this._topologyService.updateAccessGroupNode(accessNodes[a].topologyGroupId, accessObj).subscribe( (res) => {
                        if (res[0] && res[0].code === 20001) {
                            this.snackBar.open('Error Occurred. Pls try again later.', '', {
                                duration: 3000,
                                horizontalPosition: 'right',
                                panelClass: 'snack__error'
                            });
                            return;
                        }
                        for (let b = 0, bLen = this._topologyService.accessGroupNodes.length; b < bLen; b++) {
                            if (this._topologyService.accessGroupNodes[b].groupName === prevName) {
                                this._topologyService.accessGroupNodes[b].groupName = newValue;
                                break;
                            }
                        }
                        this.updateAccessGroup();
                        this.snackBar.open(this.translate.instant('application.app.common.labels.NAME_UPDATED'), '', {
                            duration: 3000,
                            horizontalPosition: 'right',
                            panelClass: 'snack__success'
                        });
                    });
                }
            }
        }
        this.clearNodeName();
    }

    isDuplicateName(formName) {
        let value;
        if (formName === 'accessGroupEditForm') {
           value = this.accessGroupEditForm.value.groupParentName;
        } else {
           value = this.accessGroupForm.value.groupName;
        }
        let isRedundant = false;
        const accessGroups = this._topologyService.accessGroupNodes;
        for (let i =0, iLen = accessGroups.length; i < iLen; i++) {
            if (accessGroups[i].groupName === value && value !== this.nodeToEdit) {
                isRedundant = true;
            }
        }
        return isRedundant;
    }

    validateGroupName: ErrorStateMatcher = {
        isErrorState: (control: FormControl) => {
            this.isGroupNameInvalid = this.isDuplicateName('accessGroupForm');
            return this.isGroupNameInvalid;
        }
    };

    validateAccessGroupName: ErrorStateMatcher = {
        isErrorState: (control: FormControl) => {
            this.isGroupNameInvalid = this.isDuplicateName('accessGroupEditForm');
            return this.isGroupNameInvalid;
        }
    };

    // clearNodeName : reset the variables changes while editing the access group name
    clearNodeName() {
        this.isEditable = false;
        this.nodeToEdit = '';
        /*this.accessGroupForm.value.groupName = '';
        this.accessGroupEditForm.value.groupParentName = '';*/
        this.accessGroupForm = this.fb.group({
        });
        this.accessGroupEditForm = this.fb.group({
        });
        this.addFormValidations();
    }
    valChangeFunction(evt, gName, fName) {
        const valObj = {};
        valObj[fName] = evt;
        this[gName].patchValue(valObj, {onlySelf: true});
    }

    // addToTopologyNodesArr :  adds the dragged assigned node to the topology tree of assigned nodes
    addToTopologyNodesArr(res, prevParentId?: number) {
       const draggedNode = res[0].nodeId;
       const newParentId = res[0].parentNodeId;
       const nodeKeys = Object.keys(this._topologyService.topologyNodes);
       const parentIdString = newParentId.toString();
       if (nodeKeys.indexOf(parentIdString) > -1) {
           this._topologyService.topologyNodes[newParentId].push(res[0]);
       } else {
           this._topologyService.topologyNodes[newParentId] = res;
       }
       this._topologyService.topologyNodeNames[draggedNode] = res[0];
       if (prevParentId) {
           this.deleteFromTopologyNodesArr(prevParentId, res);
       }
       this._topologyService.siteChildren = this._topologyService.getSiteChildren(this._topologyService.topologyNodeNames);
       this.setAssignedNodes(this._topologyService.topologyNodes);
    }

    // deleteFromTopologyNodesArr : updates the topology nodes data
    deleteFromTopologyNodesArr(prevParentId, res) {
       const node = res[0];
       const newArr = this._topologyService.topologyNodes[prevParentId];
       if (newArr.length === 1) {
           for (let i = 0, iLen = newArr.length; i < iLen; i++) {
               if (node.nodeId === newArr[i].nodeId) {
                   newArr.splice(i, 1);
                   break;
               }
           }
           if (newArr.length === 0) {
               delete this._topologyService.topologyNodes[prevParentId];
           }
       } else if (newArr.length > 1) {
           for (let i = 0, iLen = newArr.length; i < iLen; i++) {
               if (node.nodeId === newArr[i].nodeId) {
                   newArr.splice(i, 1);
                   break;
               }
           }
           this._topologyService.topologyNodes[prevParentId] = newArr;
       }
    }

    isTerminal(draggedNodeId) {
        let isNodeATerminal = false;
        for (const nodeObj in this._topologyService.topologyNodeNames) {
            if (this._topologyService.topologyNodeNames.hasOwnProperty(nodeObj) && parseInt(nodeObj, 10) === draggedNodeId) {
                if (this._topologyService.topologyNodeNames[nodeObj].type === 1000) {
                    isNodeATerminal = true;
                }
            }
        }
        return isNodeATerminal;
    }

    nodeAlreadyExists(draggedNodeId, node) {
        let isNodePresent = false;
        for (let i = 0, iLen = this._topologyService.accessGroupNodes.length; i < iLen; i++) {
            const accessNode =  this._topologyService.accessGroupNodes[i];
            if (accessNode.groupName === this.flatNodeMap.get(node).item) {
                if (accessNode.topologyNodeIds.indexOf(draggedNodeId) > -1) {
                   isNodePresent = true;
                }
            }
        }
        return isNodePresent;
    }

    openConfirmDeleteBox(node, accessNodes, nodeNames): void {
        const dialogRef = this.dialog.open(ConfirmDeleteComponent, {
            width: dialogSize.small,
            data: {nodeToDelete: node}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result && result === true) {
                if (node.level === 0) {
                    this.removeAccessGroup(node, accessNodes);
                } else if (node.level === 1) {
                    this.removeAccessGroupChild(node, accessNodes, nodeNames);
                }
            }
        });
    }

    isTerminalOfPit(node): boolean {
        const parentID = this.nodeNames[node.item].parentNodeId;
        const parentType = this.nodeNames[parentID].type;
        return parentType === 1200;
    }
}
