import {Injectable} from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class SortingService {
    sortObj: object;

    constructor() {
    }

    getSortObject(sort): object {
        this.sortObj = {sortOrder: sort.direction.toUpperCase(), sortField: sort.active};
        return this.sortObj;
    }
}
