import {Injectable} from '@angular/core';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {urls} from '../constants/urls';

@Injectable()
export class AlertService {
    constructor(private _http: HttpClient) {
    }

    getAlertsData(url, options: {}): Observable<HttpResponse<Object>> {
        return this._http.get<HttpResponse<Object>>(url, options);
    }

    getEventData(): void {

    }

    getAlertCounts(topologyId): Observable<[]> {
        const url = `${urls.alert.alertCountsPath}?topologyId=${topologyId}`;
        return this._http.get<[]>(url);
    }
    getAlertCountsByCategory(alertStatus, topologyId, category): Observable<[]> {
        const url = `${urls.alert.alertCountsByCategoryPath}?alertStatus=${alertStatus}&topologyIds=${topologyId}&category=${category}`;
        return this._http.get<[]>(url);
    }

    getAlertCountsByCategoryVG(alertStatus, topologyId, category): Observable<[]> {
        const url = `${urls.alert.alertCountsByCategoryPath}?alertStatus=${alertStatus}&topologyGroupId=${topologyId}&category=${category}`;
        return this._http.get<[]>(url);
    }

    changeStatus(statusUrl, statusObj): Observable<HttpResponse<Object>> {
        return this._http.post<HttpResponse<Object>>(statusUrl, statusObj);
    }

    changeSeverity(): void {

    }

    addNote(noteUrl, noteObj): Observable<HttpResponse<Object>> {
        return this._http.post<HttpResponse<Object>>(noteUrl, noteObj);
    }

    saveCustomerKnowledgeBase(baseUrl, baseObj): Observable<HttpResponse<Object>> {
        return this._http.post<HttpResponse<Object>>(baseUrl, baseObj);
    }

    saveTableCustomerKnowledgeBase(baseUrl, baseObj): Observable<HttpResponse<Object>> {
        return this._http.put<HttpResponse<Object>>(baseUrl, baseObj);
    }

    getAlertConfiguration(options = {}) {
        return this._http.get(urls.alert.alertConfigurationPath, options);
    }

    updateAlertConfiguration(data = {}, params = {}) {
        return this._http.post(urls.alert.alertConfigurationPath, data, params);
    }

    getTableAlertsCount(options: {}) {
        return this._http.get(urls.alert.tableAlerts, options);
    }
}
