import { Injectable } from '@angular/core';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {urls} from '../constants/urls';

@Injectable({
  providedIn: 'root'
})
export class PromotionService {

  constructor(private http: HttpClient) { }

  getPromotions(options: {}): Observable<HttpResponse<Object>> {
    const url = `${urls.promotion.promotionPath}`;
    return this.http.get<HttpResponse<Object>>(url, options);
  }

  changeStatus(promotionId, options: {}): Observable<HttpResponse<Object>> {
    const url = `${urls.promotion.promotionUpdatePath}${promotionId}`;
    return this.http.put<HttpResponse<Object>>(url, options);
  }

  createPromotion(body: {}, options?: {}): Observable<HttpResponse<Object>> {
    const url = `${urls.promotion.promotionPath}`;
    return this.http.post<HttpResponse<Object>>(url, body, options);
  }

  managePromotionEnabled(): Observable<HttpResponse<Object>> {
    const url = `${urls.promotion.managePromotionEnabled}`;
    return this.http.get<HttpResponse<Object>>(url);
  }

  getWinners(options: {}): Observable<HttpResponse<Object>> {
    const url = `${urls.promotion.winners}`;
    return this.http.get<HttpResponse<Object>>(url, options);
  }
  getEligibleDays(options: {}) {
      const url = `${urls.promotion.promotionsEligibleDays}`;
      return this.http.get<HttpResponse<Object>>(url, options);
  }

  /**
     * this function get the comp api data and path is end point of api like Properties,CompType etc..
     * @param path
     * @param options
  */
  getCompApi(path, options: {}) {
     const url = `${urls.promotion.promotionsCompApi}${path}`;
     return this.http.get<HttpResponse<Object>>(url, options);
  }
}
