import { Injectable } from '@angular/core';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {urls} from '../constants/urls';
import {TopologyStatisticsTabularView} from '../interface/topology-types.interface';

@Injectable({
  providedIn: 'root'
})
export class TableDashboardService {

  constructor(private http: HttpClient) {
  }
    getPlayers(options: {}): Observable<HttpResponse<Object>> {
      // const url = 'http://172.31.2.102:8080/api/table/v1/players';
        const url = urls.table.playerSearch;
        return this.http.get<HttpResponse<Object>>(url, options);
    }
    getBetsUsingUuid(options: {}): Observable<HttpResponse<Object>> {
        const url = urls.game.bets;
        return this.http.get<HttpResponse<Object>>(url, options);
    }
    getPlayerById(playerId, options: {}): Observable<HttpResponse<Object>> {
        const url = urls.table.playerSearch + '/' + playerId;
        return this.http.get<HttpResponse<Object>>(url, options);
    }
    getSessionDetailBySessionID(sessionId , options: {}): Observable<HttpResponse<Object>> {
        const finalUrl = urls.game.sessions + '?&sessionId=' + sessionId;
        return this.http.post<HttpResponse<Object>>(finalUrl , options) ;
    }
    getSessionDetailSessionsByIds(sessionIdAry , options: {}): Observable<HttpResponse<Object>> {
        const finalUrl = urls.game.sessionsByIds + '?sessionIds=' + sessionIdAry + '&identifier=2';
        return this.http.post<HttpResponse<Object>>(finalUrl , options) ;
    }
    updatePlayer(reqObj , options: {}): Observable<HttpResponse<Object>> {
      const finalUrl = urls.casinoMgr.updatePlayer + '?&UpdatedPlayerId=' + reqObj.UpdatedPlayerId +
          '&gamingDay=' + reqObj.gamingDay + '&topologyId=' + reqObj.topologyId
          + ' &userId=1&updateTime= ' + reqObj.updateTime + '&sessionIds=' + reqObj.sessionIds
          + '&identifier=2&ircNumbers=' + reqObj.ircNumbers;
        return this.http.post<HttpResponse<Object>>(finalUrl, options) ;
    }
    updateIrcNumber(body): Observable<HttpResponse<Object>> {
        const finalUrl = urls.game.updateIrcNumber;
        return this.http.post<HttpResponse<Object>>(finalUrl , body) ;
    }
    updateNotes(body): Observable<HttpResponse<Object>> {
        const finalUrl = urls.game.sessionNote;
        return this.http.post<HttpResponse<Object>>(finalUrl , body) ;
    }
    updateManualRating(body, sessionId): Observable<HttpResponse<Object>> {
       // const finalUrl = 'http://172.31.2.102:8080/api/game/v1/manualRatings' + sessionId;
        const finalUrl = urls.game.manualRatings + '/' + sessionId;
        return this.http.post<HttpResponse<Object>>(finalUrl , body) ;
    }
    createManualRating(body): Observable<HttpResponse<Object>> {
      //  const finalUrl = 'http://172.31.2.102:8080/api/game/v1/manualRatings';
        const finalUrl = urls.game.manualRatings;
        return this.http.post<HttpResponse<Object>>(finalUrl , body) ;
    }
}
