import {protocol, webServerDNS} from './url.common.constants';

export interface UrlConfigurationConstantsInterface {
    categories: string;
    configurations: string;
    properties: string;
    luckyChipEnabled: string;
    globalAnonymousHostEnabled: string;
    insuranceEnabled: string;
}

const basePath = '/api/configuration';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;

export const configuration: UrlConfigurationConstantsInterface = {
    categories: baseUrl + 'categories/',
    configurations: baseUrl + 'configurations/',
    properties: baseUrl + 'properties/',
    luckyChipEnabled: baseUrl +
    'configurations/?propertyCodes=com.wdts.bonus.luckyChip.enabled&templateTypeCode=SYSTEM&type=TEMPLATE',
    globalAnonymousHostEnabled: baseUrl +
    'configurations/?propertyCodes=com.wdts.anonymous.session.alert.enabled&templateTypeCode=SYSTEM&type=TEMPLATE',
    insuranceEnabled: baseUrl + 'configurations/insurance-bet-enabled/status'
};

