import {Injectable} from '@angular/core';
import {HttpBackend, HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';
import {urls} from '../../constants/urls';

@Injectable({
    providedIn: 'root'
})
export class AuthService {
    private http: HttpClient;
    constructor(httpBackend: HttpBackend) {
        this.http = new HttpClient(httpBackend);
    }

    getRefreshToken(clientId): Observable<any> {
        const authValues: any = JSON.parse(localStorage.getItem('authValues'));
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json', 'Authorization': 'Bearer ' +
                    localStorage.getItem(authValues.jwtTokenKey)
            }),
            params: {
                client_id: clientId
            }
        };
        return this.http.post(urls.auth.refresh, null, httpOptions);
    }

    logout() {
        const authValues: any = JSON.parse(localStorage.getItem('authValues'));
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json', 'Authorization': 'Bearer ' +
                    localStorage.getItem(authValues.jwtTokenKey)
            })
        };
        return this.http.post(urls.auth.logout, null, httpOptions);
    }
}
