import {protocol, webServerDNS} from './url.common.constants';

export interface UrlTableConstantsInterface {
  playerSearch: string;
}

const basePath = '/api/table';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;
export const table: UrlTableConstantsInterface = {
    playerSearch: baseUrl + 'players/'
};
