import {protocol, webServerDNS} from './url.common.constants';

export interface UrlCageConstantsInterface {
    chipsInventory: string;
    localGamingDay: string;
    nextRollDtm: string;
    gamingDays: string;
    buyInTransaction: string;
    chipTrayScan: string;
    paginatedOpenerCloser: string;
    missingChips: string;
    missingChipScan: string;
    getScanCompDetail: string;
    chipTrayOpenerCloser: string;
    openerCloserReport: string;
    printReport: string;
    getfcTransactions: string;
    gamingDay: string;
}

const basePath = '/api/chip';
const cageBasePath = '/api/cage';
const printBasePath = '/api/print';
const casinoBasePath = '/api/casinomanager';
const version = '/v1/';
const transactions = 'transactions';
const baseUrl = protocol + webServerDNS + basePath + version;
const cageBaseUrl = protocol + webServerDNS + cageBasePath + version;
const casinoBaseUrl = protocol + webServerDNS + casinoBasePath + version;
const printBaseUrl = protocol + webServerDNS + printBasePath + version;

export const cage: UrlCageConstantsInterface = {
    chipsInventory: baseUrl + 'chipTrayInventory/',
    localGamingDay: casinoBaseUrl + 'localGamingDay',
    nextRollDtm: casinoBaseUrl + 'nextRollDtm',
    gamingDays: casinoBaseUrl + 'localGamingDaysInHierarchy',
    buyInTransaction: baseUrl + transactions,
    chipTrayScan: baseUrl + 'chipTrayScans/table/',
    paginatedOpenerCloser: baseUrl + 'paginatedOpenerCloser',
    missingChips: baseUrl + 'missingChipList',
    missingChipScan: baseUrl + 'getScanDetail',
    getScanCompDetail: baseUrl + 'getScanCompDetail',
    chipTrayOpenerCloser: baseUrl + 'chipTrayOpenerCloser',
    openerCloserReport: baseUrl + 'openerCloserReportData',
    printReport: printBaseUrl + 'renderReport',
    getfcTransactions: cageBaseUrl + 'fcTransactions',
    gamingDay: '2019-03-28'
};
