import {Component, EventEmitter, Input, OnChanges, OnInit, Output} from '@angular/core';
import {AuthService} from '../services/Authentication/auth.service';
import {UserService} from '../services/user.service';
import {DecodedTokenService} from '../services/Authentication/decoded-token.service';
import {
    protocol,
    treasuryPort,
    webAlertsTLSPort,
    webCamTLSPort,
    webCashierTLSPort,
    webCasinoManagerTLSPort,
    webConfigurationTLSPort,
    webPlayerDashboardTLSPort,
    webServerDNS,
    webTableDashboardTLSPort
} from '../constants/url.common.constants';
import {UtilityService} from '../services/utility.service';
import {ConfigurationService} from '../services/configuration.service';

@Component({
    selector: 'app-list',
    templateUrl: './app-list.component.html',
    styleUrls: ['./app-switcher.component.scss']
})
export class AppListComponent implements OnInit {
    @Input('currentAppCode') currentAppCode;
    appSwitcher: any;
    applicationCodesFromJwt: Array<string>;
    loading = false;
    callInProg = false;

    constructor(private authService: AuthService,
                private userService: UserService,
                private utilities: UtilityService,
                private configurationService: ConfigurationService,
                private decodedTokenService: DecodedTokenService, public utilService: UtilityService) {
    }

    ngOnInit() {
        this.checkGlobalCbpt().subscribe(res => {
            this.utilService.isCBPT = 'true' === res[0].propertyValues[0].propertyValue.toLowerCase();
            this.initMenuItems();
            this.loading = true;
            this.appSwitcher.menuRows = [...this.appSwitcher.appMenuItems];
            this.applicationCodesFromJwt = this.decodedTokenService.getDecodedJwtToken().applications;
            this.appSwitcher.favoriteApp = this.decodedTokenService.getDecodedJwtToken().defaultApplication;
            /*if (localStorage.getItem('favoriteApp') !== null && localStorage.getItem('favoriteApp') !== undefined) {
                this.appSwitcher.favoriteApp = localStorage.getItem('favoriteApp');
            }*/
            this.divideMenuItems();
        }, error => {
            this.loading = true;
            console.error(error);
        });
    }

    initMenuItems() {
        this.appSwitcher = {
            appMenuItems: [
                {
                    applicationCode: 'ALERTS',
                    key: 'alrt',
                    storeKey: 'jwt_alert',
                    name: 'Alerts',
                    permission: 'ACCESS_ALERTS_APP',
                    iconClass: 'app-icon__alerts',
                    translateLabel: 'application.app.common.labels.apps.ALERTS',
                    openUrl: webAlertsTLSPort,
                    isVisible: true
                },
                {
                    applicationCode: 'CAM',
                    key: 'cam',
                    storeKey: 'jwt_cam',
                    name: 'CAM',
                    permission: 'ACCESS_CAM_APP',
                    iconClass: 'app-icon__cam',
                    translateLabel: 'application.app.common.labels.apps.CAM',
                    openUrl: webCamTLSPort,
                    isVisible: false
                },
                {
                    applicationCode: 'CASHIER',
                    key: 'cash',
                    storeKey: 'jwt_cash',
                    name: 'Cashier',
                    permission: 'ACCESS_CASHIER_APP',
                    iconClass: 'app-icon__cashier',
                    translateLabel: 'application.app.common.labels.apps.CASHIER',
                    openUrl: webCashierTLSPort,
                    isVisible: false
                },
                {
                    applicationCode: 'CONFIGURATION',
                    key: 'conf',
                    storeKey: 'jwt_config',
                    name: 'Configuration',
                    permission: 'ACCESS_CONFIG_APP',
                    iconClass: 'app-icon__configuration',
                    translateLabel: 'application.app.common.labels.apps.CONFIG',
                    openUrl: webConfigurationTLSPort,
                    isVisible: true
                },
                {
                    applicationCode: 'CASINO_MGR',
                    key: 'cmr',
                    storeKey: 'jwt_cmr',
                    name: 'Casino Manager',
                    permission: 'ACCESS_CASINO_MGR_APP',
                    iconClass: 'app-icon__casino-manager',
                    translateLabel: 'application.app.common.labels.apps.CMGR',
                    openUrl: webCasinoManagerTLSPort,
                    isVisible: true
                },
                {
                    applicationCode: 'TREASURY_MGR',
                    key: 'trs',
                    storeKey: 'jwt_trs',
                    name: 'Treasury',
                    permission: 'ACCESS_TREASURY_MGR_APP',
                    iconClass: 'app-icon__treasury',
                    translateLabel: 'application.app.common.labels.apps.TRSMGR',
                    openUrl: treasuryPort,
                    isVisible: true
                },
                {
                    applicationCode: 'PLAYER_DASH',
                    key: 'plr',
                    storeKey: 'jwt_plr',
                    name: 'Player Dashboard',
                    permission: 'ACCESS_PLAYER_DASH',
                    iconClass: 'app-icon__player-dashboard',
                    translateLabel: 'application.app.common.labels.apps.PLRDB',
                    openUrl: webPlayerDashboardTLSPort,
                    isVisible: this.utilService.isCBPT
                },
                {
                    applicationCode: 'TABLE_DASH',
                    key: 'tbldash',
                    storeKey: 'jwt_tbldash',
                    name: 'Table Dashboard',
                    permission: 'ACCESS_TABLE_DASH',
                    iconClass: 'app-icon__table-dash',
                    translateLabel: 'application.app.common.labels.apps.TBLDB',
                    openUrl: webTableDashboardTLSPort,
                    isVisible: false
                },
                {
                    applicationCode: 'REPORTS',
                    key: 'rpt',
                    storeKey: 'jwt_rpt',
                    name: 'BI Application',
                    permission: 'ACCESS_REPORTS_APP',
                    iconClass: 'app-icon__reports',
                    translateLabel: 'application.app.common.labels.apps.BI',
                    openUrl: 784,
                    isVisible: true
                }
            ],
            menuRows: []
        };
    }

    divideMenuItems() {
        this.appSwitcher.menuRows = [];
        this.appSwitcher.appMenuItems.forEach(menuItem => {
            if (menuItem.applicationCode !== this.currentAppCode && menuItem.isVisible) {
                this.appSwitcher.menuRows.push(menuItem);
            }
        });
    }

    checkGlobalCbpt() {
        return this.configurationService.checkGlobalCbpt();
    }

    setFavoriteApp(event, appCode) {
        if (appCode === this.appSwitcher.favoriteApp) {
            return;
        } else {
            this.callInProg = true;
            event.preventDefault();
            event.stopPropagation();
            const decodedToken = this.decodedTokenService.getDecodedJwtToken();
            const userId = decodedToken.userId;
            const obj = {
                userId: (userId || 1),
                defaultApplication: {
                    applicationCode: appCode
                }
            };

            this.userService.setFavoriteApp(obj, userId)
                .subscribe(response => {
                    this.appSwitcher.favoriteApp = ( response['defaultApplication']['applicationCode'] || appCode );
                    // localStorage.setItem('favoriteApp', ( response['defaultApplication']['applicationCode'] || appCode));
                    this.callInProg = false;
                });
        }
    }

    openApp(clientID) {
        this.authService.getRefreshToken(clientID).subscribe(tokenData => {
            this.currentApp(clientID, tokenData);
            this.utilService.closeDialog.next('close');
        });
    }

    currentApp(clientID, tokenData) {
        let accessToken = '';
        const url = protocol + webServerDNS;
        if (clientID && tokenData && tokenData.access_token) {
            accessToken = '#/access_token=' + tokenData.access_token + '&token_type=' + tokenData.token_type + '&expires_in='
                + tokenData.expires_in + '&scope=' + tokenData.scope;
        }
        const authValues = JSON.parse(localStorage.getItem('authValues'));
        let target: string;
        if (authValues) {
            if (authValues.clientId === 'lgn') {
                sessionStorage.clear();
                localStorage.clear();
                target = authValues.clientId = '_self';
            } else {
                target = authValues.clientId = '_blank';
            }
        }

        switch (clientID) {
            case 'alrt':
                window.open(`${url}:${webAlertsTLSPort}/${accessToken}`, target);
                break;
            case 'conf':
                window.open(`${url}:${webConfigurationTLSPort}/${accessToken}`, target);
                break;
            case 'cmr':
                window.open(`${url}:${webCasinoManagerTLSPort}/${accessToken}`, target);
                break;
            case 'trs':
                window.open(`${url}:${treasuryPort}/${accessToken}`, target);
                break;
            case 'plr':
                window.open(`${url}:${webPlayerDashboardTLSPort}/${accessToken}`, target);
                break;
            case 'rpt':
                window.open(`${url}/jasperserver-pro/login.html`, target);
                break;
        }

    }
}
