import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {HttpBackend, HttpClient, HttpHeaders, HttpResponse} from '@angular/common/http';
import {urls} from '../constants/urls';
import {
    ChipsInventoryInterface,
    LocalGamingDayInterface,
    LocalGamingDaysInHeirarchyInterface,
    RollTimeInterface,
    RollTimePayloadInterface,
    ChipTrayOpenerCloser,
    OpenerCloserReportData,
    FcTxnsInterface
} from '../interface/cage.interface';

@Injectable({
    providedIn: 'root'
})
export class CageService {
    constructor(private http: HttpClient) {

    }

    getInventoryData(id, options: {}): Observable<ChipsInventoryInterface[]> {
        return this.http.get<ChipsInventoryInterface[]>
        (urls.cage.chipsInventory + id, options);
    }

    getCurrentGamingDay(topologyId): Observable<HttpResponse<Object>> {
        const url = `${urls.cage.localGamingDay}?topologyId=${topologyId}`;
        return this.http.get<HttpResponse<Object>>(url);
    }

    getLocalGamingDay(id): Observable<LocalGamingDayInterface> {
        return this.http.get<LocalGamingDayInterface>(urls.cage.localGamingDay,
            {
                params: {
                    topologyId: id
                }
            });
    }

    getRollTimeData(options: {}): Observable<RollTimeInterface[]> {
        const url = urls.cage.nextRollDtm;
        return this.http.get<RollTimeInterface[]>(url, options);
    }

    getRollDTM(topologyId): Observable<any> {
        const url = `${urls.cage.nextRollDtm}?topologyId=${topologyId}`;
        return this.http.get<any>(url);
    }

    localGamingDaysInHierarchy(topologyId): Observable<LocalGamingDaysInHeirarchyInterface> {
        const url = `${urls.cage.gamingDays}?topologyId=${topologyId}`;
        return this.http.get<LocalGamingDaysInHeirarchyInterface>(url);
    }

    postRollTimeData(payLoad: {}, userId = '', employeeId = ''): Observable<HttpResponse<RollTimePayloadInterface>> {
        let url = urls.cage.nextRollDtm;
        if (userId && employeeId) {
            url += `?userId=${userId}&employeeId=${employeeId}`;
        }
        return this.http.post<HttpResponse<RollTimePayloadInterface>>(url, payLoad);
    }

    getChipTrayOpenerCloser(options: {}): Observable<ChipTrayOpenerCloser> {
        const url = urls.cage.chipTrayOpenerCloser;
        return this.http.get<ChipTrayOpenerCloser>(url, options);
    }

    getOpenerCloserReportData(options: {}): Observable<OpenerCloserReportData> {
        const url = urls.cage.openerCloserReport;
        return this.http.get<OpenerCloserReportData>(url, options);
    }

    getReportUrl(data) {
        const url = `${urls.cage.printReport}?print=true&format=PDF&reportName=OpenerCloserForm&reportData=${data}`;
        return url;
    }

    getReportData(data): Observable<HttpResponse<any>> {
        const url = urls.cage.printReport;
        const payLoad = {print: true, format: 'PDF', reportName: 'OpenerCloserForm', reportData: JSON.stringify(data)};
        const httpOptions = {
            'responseType': 'arraybuffer' as 'json'
        }
        return this.http.post<HttpResponse<any>>(url, payLoad, httpOptions);
    }

    getfcTransactions(options: {}): Observable<FcTxnsInterface> {
        console.log('common options', options)
        const url = urls.cage.getfcTransactions;
        return this.http.get<FcTxnsInterface>(url, options);
    }
}
