import {
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnInit,
    Output,
    QueryList,
    ViewChildren,
    SimpleChange,
    ElementRef,
    ViewChild,
    AfterViewChecked
} from '@angular/core';
import {FilterComponent} from '../filter/filter.component';
import {OWL_DATE_TIME_FORMATS} from 'ng-pick-datetime';
import { MatSnackBar } from '@angular/material/snack-bar';
import {TranslateService} from '@ngx-translate/core';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {equalIgnoreCase} from '../utils';
import {Observable} from 'rxjs';
import {FormControl} from '@angular/forms';
import {map, startWith} from 'rxjs/operators';
import {WDTSUtility} from '../utils/input-validation-utils';

export const MY_CUSTOM_FORMATS = {
    fullPickerInput: {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: 'numeric',
        hour12: false
    },
    datePickerInput: {year: 'numeric', month: 'short', day: 'numeric', hour12: false},
    timePickerInput: {hour: 'numeric', minute: 'numeric', hour12: false},
    monthYearLabel: {year: 'numeric', month: 'short'},
    dateA11yLabel: {year: 'numeric', month: 'long', day: 'numeric'},
    monthYearA11yLabel: {year: 'numeric', month: 'long'},
};

@Component({
    selector: 'app-tables',
    templateUrl: './app-tables.component.html',
    styleUrls: ['./app-tables.component.scss'],
    providers: [
        {provide: OWL_DATE_TIME_FORMATS, useValue: MY_CUSTOM_FORMATS}
    ],
    animations: [
        trigger('detailExpand', [
            state('collapsed', style({height: '0px', minHeight: '0', display: 'none'})),
            state('expanded', style({height: '*'})),
            transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
        ]),
    ]
})

export class AppTablesComponent implements OnInit, OnChanges, AfterViewChecked {
    @ViewChild('input01') input01: ElementRef;
    @ViewChild('input02') input02: ElementRef;
    @ViewChild('input03') input03: ElementRef;
    @ViewChild('input04') input04: ElementRef;
    @ViewChild('domdateTime4') domdateTime4;
    rowHeader = [];
    isCheckedAll = false;
    sortObj = {
        active: '',
        sortField: '',
        sortOrder: ''
    };
    selectedFilter = {};
    perPageOption = [10, 20, 30, 50, 100];
    perPageLimit;
    isFilterOpen = false;
    loading = false;
    noDataAvailable = false;
    bulkUpdateActive = false;
    fromTime: Date;
    toTime: Date;
    toMinTime: Date;
    toMaxTime: Date;
    fromToFilter = [];
    fromTimeArr = [];
    toTimeArr = [];
    promoFromDate: Date;
    promoToDate: Date;
    minStartTime: Date;
    valueTextAutofill;
    isDblClicked = false;
    indexDblClicked: number;
    changedLabel: string;
    editedCol: string;
    editableColumnWidth = false;
    disableFilterButton = true;
    headerSelected: string;
    expandedElement;
    clearFilterFlag;

    @ViewChildren(FilterComponent) filterComponents: QueryList<FilterComponent>;

    @Input() tableRows = [];
    @Input() tableHeaders = [];
    @Input() totalRecords = -1;
    @Input() filterOptions = [];
    @Input() editableTable = false;
    @Input() tableTitle = 'application.app.ALERTS_LABELS.ALERTS';
    @Input() isPitClicked = false;
    @Input() alternateTitle ?;
    @Input() tableTitleNotRequired ?;
    @Input() gamingDay ?;
    @Input() nodeType: string;
    @Input() tabName: string;
    @Input() description: string;
    @Input() playerPositions: string;
    @Input() hidePagination: boolean;
    @Input() tableExpandableRows = [];
    @Input() hideFilters: boolean;
    @Input() showFilters: boolean;
    @Input() inputStart: number;
    @Input() inputCurrentPage: number;
    @Input() inputLimit: number;
    @Output() checkAll = new EventEmitter();
    @Output() checkBoxEvent = new EventEmitter();
    @Output() checkBoxAllEvent = new EventEmitter();
    @Output() node = new EventEmitter();
    @Output() filter = new EventEmitter();
    @Output() filterChange = new EventEmitter();
    @Output() pagination = new EventEmitter();
    @Output() sort = new EventEmitter();
    math = Math;
    deletePPId;
    textAutofillControl = new FormControl();
    options: string[];
    filteredOptionsAutofillControl: Observable<string[]>;

    constructor(public snackBar: MatSnackBar, public translate: TranslateService) {
        // this.perPageLimit = this.perPageOption[0];
    }

    ngOnInit() {
        if (this.tableHeaders) {
            this.setRowHeaders();
        }
        if (this.filterOptions) {
            this.setSelectedFilters();
        }
        this.filteredOptionsAutofillControl = this.textAutofillControl.valueChanges
            .pipe(
                startWith(''),
                map(value => this._filter(value))
            );
    }

    ngOnChanges(changes: { [propKey: string]: SimpleChange }) {
        this.isCheckedAll = false;
        // If condition is for GR-2846: which shows the loader while API request, once we get response loader will be hidden.
        if (changes && changes.tableRows && (changes.tableRows.currentValue !== changes.tableRows.previousValue)) {
            this.isCheckedAll = false;
            if (typeof this.totalRecords === 'undefined') {
                this.loading = true;
            } else if (this.totalRecords === 0) {
                this.noDataAvailable = true;
                this.loading = false;
            } else {
                this.noDataAvailable = this.totalRecords === 0;
                this.loading = this.tableRows && this.tableRows.length === 0 && !this.noDataAvailable;

                if (this.tableHeaders && (!this.rowHeader || this.rowHeader.length === 0 || this.rowHeader.length !== this.tableHeaders.length)) {
                    this.tableHeaders.map((item) => {
                        if (this.rowHeader.indexOf(item.title) === -1) {
                            this.rowHeader.push(item.title);
                        }
                    });
                }
                this.updateRowStatus();
                this.setSelectedFilters();
            }
        }
    }

    private _filter(value: string): string[] {
        const filterValue = value.toString().toLowerCase();
        if (this.options) {
            return this.options.filter(option => option.toString().toLowerCase().includes(filterValue));
        }
    }

    checkForDisable(event) {
        let isChanged = false;
        if (this.textAutofillControl.value && this.textAutofillControl.value !== '') {
            this.disableFilterButton = false;
        }
        if (!event || event.length === 0) {
            this.deletePPId = true;
            delete this.selectedFilter['ccasIds'];
            this.valueTextAutofill = '';
            isChanged = true;
        }
        if (isChanged) {
            this.filterChange.emit(this.selectedFilter);
        }
    }

    updateRowStatus() {
        if (this.tableRows && this.tableRows.length) {
            this.tableRows.map((node) => {
                node.checked = this.isCheckedAll;
                if (typeof node.alertSeverity === 'undefined') {
                    node.alertSeverity = 'NONE';
                }
            });
        }
    }

    setRowHeaders() {
        this.rowHeader = [];
        this.tableHeaders.map((item) => {
            if (item.title) {
                if (this.rowHeader.indexOf(item.title) === -1) {
                    this.rowHeader.push(item.title);
                }

            }
            if (item.sortFirst) {
                this.sortObj = {
                    active: item.title,
                    sortField: item.title,
                    sortOrder: item.sortOrder || 'asc'
                };
            }
            if (item.permission === undefined || item.permission === null) {
                item.permission = true;
            }
        });
    }

    setSelectedFilters() {
        this.filterOptions.map((filter) => {
            if (filter.selectedOptions.length && !this.clearFilterFlag) {
                this.selectedFilter[filter.column] = filter.selectedOptions;
                this.disableFilterButton = false;
                if (filter.column === 'minStartTime' && !this.minStartTime) {
                    this.minStartTime = new Date(filter.selectedOptions);
                }
                if (!this.deletePPId && filter.column.toLowerCase() === 'ccasids') {
                    this.valueTextAutofill = filter.selectedOptions;
                }
            }
        });
        this.filterOptions = JSON.parse(JSON.stringify(this.filterOptions));
        if (this.filterOptions && this.filterOptions.length > 0) {
            if (this.filterOptions.find(o => o.column.toLowerCase() === 'ccasids')) {
                this.options = this.filterOptions.find(o => o.column.toLowerCase() === 'ccasids').options;
                this.valueTextAutofill = this.filterOptions.find(ob => ob.column=='ccasIds').selectedOptions
            }
        }
    }

    checkAllRows(): void {
        const freezedRows = this.tableRows.filter(elm => {
            return ( elm['isDisable'] && elm['isDisable'] === true );
        });
        if (freezedRows.length === this.tableRows.length) {
            return;
        } else {
            this.isCheckedAll = !this.isCheckedAll;
            this.bulkUpdateActive = this.isCheckedAll;
            this.tableRows.map((node) => {
                node.checked = this.isCheckedAll;
            });
            this.checkAll.emit(this.tableRows);
            this.checkBoxAllEvent.emit(this.isCheckedAll);
        }
    }

    bulkCheckBoxClickEvt(e): boolean {
        const freezedRows = this.tableRows.filter(elm => {
            return ( elm['isDisable'] && elm['isDisable'] === true );
        });
        if (freezedRows.length === this.tableRows.length) {
            this.unCheckAllRows();
            e.preventDefault();
            return false;
        }
        /* else {this.checkAllRows();}*/
    }

    unCheckAllRows(): void {
        this.isCheckedAll = false;
        this.bulkUpdateActive = this.isCheckedAll;
        this.tableRows.map((node) => {
            node.checked = this.isCheckedAll;
        });
        this.checkBoxAllEvent.emit(this.isCheckedAll);
    }

    updateEventObj(obj) {
        if (obj && obj.isDisable === true) {
            return;
        } else {
            this.node.emit(obj);
        }
    }

    sortData(obj) {
        this.resetValues();
        this.editableColumnWidth = false;
        if (obj.active && obj.direction) {
            this.sortObj = obj;
            this.sortObj.sortOrder = obj.direction;
            this.sortObj.sortField = obj.active;
        }
        this.sort.emit(obj);
    }

    updateFilter(obj) {
        this.disableFilterButton = false;
        this.selectedFilter[obj.columnName] = obj.selectedOption;
        this.filterChange.emit(this.selectedFilter);
    }

    applyFilter() {
        this.loading = true;
        this.clearFilterFlag = false;
        // this.disableFilterButton = true;
        const obj = {
            selectedFilter: this.selectedFilter,
            state: 'apply'
        };
        if (this.fromToFilter.length) {
            if (this.fromToFilter[0] && this.fromToFilter[0] !== null) {
                obj.selectedFilter['fromDate'] = new Date(this.fromToFilter[0]).toISOString();
            }
            if (this.fromToFilter[1] && this.fromToFilter[1] !== null) {
                obj.selectedFilter['toDate'] = new Date(this.fromToFilter[1]).toISOString();
            }
        }
        /*From Time Filter*/
        if (this.fromTime && this.fromTime !== null) {
            this.fromTimeArr = new Date(this.fromTime).toTimeString().split(':');
            obj.selectedFilter['minStartTime'] = this.fromTimeArr[0] + ':' + this.fromTimeArr[1];
        }
        /*To Time Filter*/
        if (this.toTime && this.toTime !== null) {
            this.toTimeArr = new Date(this.toTime).toTimeString().split(':');
            obj.selectedFilter['maxStartTime'] = this.toTimeArr[0] + ':' + this.toTimeArr[1];
        }
        if (this.promoFromDate && this.promoFromDate !== null) {
            obj.selectedFilter['fromDate'] = new Date(this.promoFromDate).toISOString();
        }

        if (this.promoToDate && this.promoToDate !== null) {
            obj.selectedFilter['toDate'] = new Date(this.promoToDate).toISOString();
        }

        if (this.valueTextAutofill && this.valueTextAutofill !== null) {
            obj.selectedFilter['ccasIds'] = this.valueTextAutofill;
        }

        if (this.minStartTime && this.minStartTime !== null) {
            obj.selectedFilter['minStartTime'] = this.timeForLocalGamingDay(this.minStartTime).toISOString();
        }
        this.filter.emit(obj);
    }

    timeForLocalGamingDay(time): Date {
        let localGamingDay;
        if (this.gamingDay) {
            localGamingDay = this.gamingDay.toString();
        }

        const gamingDayAry = localGamingDay.split('-');
        return new Date(Number(gamingDayAry[0]), Number(gamingDayAry[1]) - 1, Number(gamingDayAry[2]),
            time.getHours(), time.getMinutes());
    }

    clearFilter() {
        this.clearFilterFlag = true;
        this.loading = true;
        this.noDataAvailable = false;
        this.disableFilterButton = true;
        this.fromToFilter = [];
        this.selectedFilter = {};
        this.filter.emit({
            selectedFilter: [],
            state: 'clear'
        });
        // @ts-ignore
        this.promoFromDate = '';
        // @ts-ignore
        this.promoToDate = '';
        // @ts-ignore
        this.valueTextAutofill = '';
        // @ts-ignore
        this.minStartTime = '';
        // @ts-ignore
        this.fromTime = '';
        // @ts-ignore
        this.toTime = '';
        requestAnimationFrame(() => {
            this.filterComponents.forEach(filterComponent => {
                filterComponent.options = filterComponent.filterConfigOption.selectedOptions;
            });
        });
    }

    updatePagination(obj) {
        this.inputStart = obj.start;
        this.inputCurrentPage = obj.currentPage;
        this.loading = true;
        this.inputLimit = obj.limit;
        this.perPageLimit = obj.limit;
        this.unCheckAllRows();
        this.pagination.emit(obj);
    }

    bulkStatusUpdate(status) {
        const obj = {obj: [], status: status, type: 'bulk-update'};
        this.tableRows.forEach((item) => {
            if (item.checked) {
                obj.obj.push(item);
            }
        });
        this.unCheckAllRows();
        this.node.emit(obj);
    }

    updateCheckedStatus(isDisable, _index, elem) {
        if (isDisable) {
            return;
        }
        requestAnimationFrame(() => {
            if (!elem.checked) {
                elem.checked = true;
                this.bulkUpdateActive = true;
                this.isCheckedAll = true;
                for (let i = 0; i < this.tableRows.length; i++) {
                    if (!this.tableRows[i].checked) {
                        this.isCheckedAll = false;
                        break;
                    }
                }
            } else {
                this.isCheckedAll = false;
                elem.checked = false;
                this.bulkUpdateActive = false;
                for (let i = 0; i < this.tableRows.length; i++) {
                    if (this.tableRows[i].checked) {
                        this.bulkUpdateActive = true;
                        break;
                    }
                }
            }
            if (!elem['index']) {
                elem['index'] = _index;
            }
            this.checkBoxEvent.emit(elem);
        });
    }

    cancelUpdate(elem): void {
        if (elem.hasOwnProperty('isEdited')) {
            delete elem.isEdited;
        }
        this.resetValues();
    }

    resetValues() {
        this.headerSelected = '';
        this.isDblClicked = false;
        this.indexDblClicked = -1;
        this.editedCol = '';
        this.editableColumnWidth = !this.editableColumnWidth;
    }

    getLabel(oldName, value) {
        this.changedLabel = this.trimWhiteSpace(value);
    }

    labelDblClicked(isDisable, index, element, header, col?: string) {
        const headerType = header.type;
        if (isDisable) {
            return false;
        }
        if (this.editedCol && this.indexDblClicked) {
            const colAlreadyEdited = this.tableRows[this.indexDblClicked];
            if (colAlreadyEdited.hasOwnProperty('isEdited')) {
                delete colAlreadyEdited.isEdited;
            }
        }
        this.headerSelected = headerType;
        this.isDblClicked = true;
        this.indexDblClicked = index;
        this.editedCol = col;
        element.isEdited = true;
        this.editableColumnWidth = !this.editableColumnWidth;
        if (col === 'shortName') {
            window.setTimeout(() => {
                this.input01.nativeElement.focus();
            });
        } else if (col === 'label') {
            window.setTimeout(() => {
                this.input02.nativeElement.focus();
            });
        } else if (header.validation) {
            window.setTimeout(() => {
                this.input04.nativeElement.focus();
            });
        } else {
            window.setTimeout(() => {
                this.input03.nativeElement.focus();
            });
        }
    }

    updateLabel(elem, col?: string) {
        this.cancelUpdate(elem);
        if (col === 'name' || col === 'shortName' || col === 'host' || col === 'label') {
            if (this.changedLabel !== '') {
                this.updateEventObj({
                    type: 'changeLabel', obj: {
                        changedLabel: this.changedLabel,
                        elem: elem,
                        column: col
                    }
                });
            } else {
                this.throwValueError(col);
            }
        } else {
            this.updateEventObj({
                type: 'changeLabel', obj: {
                    changedLabel: this.changedLabel,
                    elem: elem,
                    column: col
                }
            });
        }
    }

    throwValueError(col) {
        if (col === 'name' || col === 'shortName' || col === 'host' || col === 'label') {
            this.snackBar.open(this.translate.instant('INPUT_CANNOT_BE_BLANK'), '', {
                duration: 3000,
                horizontalPosition: 'right',
                panelClass: 'snack__info'
            });
        }
    }

    setPath(colValue) {
        const path = colValue.replace('[', '').replace(']', '');
        const apath = path.split(',');
        let absolutePath = '';
        for (let i = apath.length - 1; i >= 0; i--) {
            absolutePath = absolutePath + '/' + apath[i];
        }
        return absolutePath;
    }

    getText(value) {
        let textValue;
        if (typeof value === 'undefined' || value === '') {
            textValue = '--';
        } else {
            textValue = value;
        }
        return textValue;
    }

    isUndefinedValue(value) {
        return typeof value === 'undefined';
    }

    trimWhiteSpace(x): string {
        return x.replace(/^\s+|\s+$/gm, '');
    }

    numberFormatterRequired(col): boolean {
        let needsFormatter = false;
        if (col === 'chipsetId' || col === 'casinoSiteId' || col === 'gamingCompanyId') {
        } else {
            needsFormatter = true;
        }
        return needsFormatter;
    }

    validateInputs(event, field) {
        switch (field) {
            case 'fromTime':
                this.toMinTime = new Date(this.fromTime);
                this.toMinTime.setTime(this.toMinTime.getTime() + 60 * 1000);
                this.toMaxTime = new Date();
                this.toMaxTime.setHours(24, 0, 0, 0);
                this.toTime = this.fromTime;
                break;
        }
    }

    /* Helper function(s) */
    isArrayEmpty(_array) {
        let retVal = true;
        if (Array.isArray(_array) && typeof _array !== 'undefined'
            && _array !== null && _array.length !== null && _array.length > 0) {
            retVal = false;
        }
        return retVal;
    }

    getRoundedNum(value) {
        const reqValue = parseFloat(value);
        return Math.round(reqValue);
    }

    concatEmpId(headerRowElem, empCode) {
        let retval = headerRowElem;
        if (this.isUndefinedValue(empCode)) {
            retval = headerRowElem;
        } else {
            retval = headerRowElem + ' (' + empCode + ')';
        }
        return retval;
    }

    /* Check for userId: 1  firstName: "PP"  lastName: "Master"  userName: "ppmaster"  employeeNumber: "0"*/
    isPPMasterRow(ELEM) {
        if (ELEM) {
            return (ELEM.employeeNumber === '0' && equalIgnoreCase(ELEM.userName, 'ppmaster', true, true) );
        } else {
            return false;
        }
    }

    getCustomTranslatedText(text: string, separator) {
        let temp;
        if (!text) {
            return '';
        }
        if (text.indexOf(separator) !== -1) {
            temp = text.split(separator);
        } else {
            return this.translate.instant(text.toUpperCase());
        }
        if (temp) {
            for (let i = 0; i < temp.length; i++) {
                temp[i] = this.translate.instant(temp[i]);
            }
        }
        return temp ? temp.join(separator) : '';
    }

    loadDefaultImage(event) {
        event.target.src = '/assets/images/player.png';
    }

    restrictNumberField(_evt, maxLen?, val?) {
        return (WDTSUtility.validateNumberField(_evt, undefined, _evt.currentTarget.value));
    }

    validateInputsTime() {
        if (this.domdateTime4 && this.domdateTime4.nativeElement.value.indexOf('24:') != -1) {
            this.domdateTime4.nativeElement.value = this.domdateTime4.nativeElement.value.replace('24:', '00:');
        }
    }

    ngAfterViewChecked() {
        if (this.domdateTime4 && this.domdateTime4.nativeElement.value.indexOf('24:') != -1) {
            this.domdateTime4.nativeElement.value = this.domdateTime4.nativeElement.value.replace('24:', '00:');
        }
    }

    getAnonymousTranslatedVal() {
        return this.translate.instant('ANONYMOUS');
    }
}
