import {protocol, webServerDNS} from './url.common.constants';

export interface UrlRolesConstantsInterface {
    roleData: string;
    permission: string;
}

const basePath = '/api/user';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;
export const roles: UrlRolesConstantsInterface = {
    roleData: baseUrl + 'roles',
    permission: baseUrl + 'permissions'
};
