import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CurrentTopologyService {
  private defaultTopologyID = 3;
  private defaultGamingDay: Date;
  public topology = new BehaviorSubject<number>(this.defaultTopologyID);
  public currentTopology =  this.topology.asObservable();
  public gamingDay = new BehaviorSubject<Date>(this.defaultGamingDay);
  public currentGamingDay =  this.gamingDay.asObservable();
  constructor() { }
  updateTopology(topologyID: number, gamingDay: Date) {
    this.topology.next(topologyID);
    this.gamingDay.next(gamingDay);
  }
}
