import {protocol, webServerDNS} from './url.common.constants';

export interface UrlPromotionConstantsInterface {
    promotionPath: string;
    promotionUpdatePath: string;
    managePromotionEnabled: string;
    winners: string;
    promotionsEligibleDays: string;
    promotionsWinnerVoucher: string;
    promotionsCompApi: string;
}

const basePath = '/api/promotion';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;
const compApiBasePath = protocol + webServerDNS + '/api/integration' + version + 'integration/'

export const promotion: UrlPromotionConstantsInterface = {
    promotionPath: baseUrl + 'promotions',
    promotionUpdatePath: baseUrl,
    managePromotionEnabled: baseUrl + 'promotion-management-enabled/status',
    winners: baseUrl + 'winners',
    promotionsEligibleDays: baseUrl + 'promotions-eligible-days',
    promotionsWinnerVoucher: baseUrl,
    promotionsCompApi: compApiBasePath
};

