import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'roundUp'
})
export class RoundUpPipe implements PipeTransform {

    transform(value: any, args?: any): any {
        if (!value) {
            return value;
        }
        value = Math.ceil(value);
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    }

}
