import {Component, Input} from '@angular/core';
import {CurrentTopologyService} from '../../services/current-topology.service';
import {AlertCount} from '../../interface/alert.interface';
import {TopologyStatistics} from '../../interface/topology-types.interface';

@Component({
    selector: 'app-subnav-data-bar',
    templateUrl: './subnav-data-bar.component.html',
    styleUrls: ['./subnav-data-bar.component.scss']
})
export class SubnavDataBarComponent {
    @Input() alertsCount: AlertCount;
    @Input() topologyStatistics: TopologyStatistics;
    private currentToplogyId: number;
    private currentGamingDay: Date;
    constructor(private currentTopology: CurrentTopologyService) {
        this.updateTopology(); // just for testing purpose
    }
    updateTopology () {
        // this has to be assigned from the route
        this.currentToplogyId = 3; // temporary value
        this.currentGamingDay = new Date('2019-01-10');
        this.currentTopology.updateTopology(this.currentToplogyId, this.currentGamingDay);
    }

    convertToNumber(value): number {
        return parseFloat(value);
    }
}
