import {Injectable} from '@angular/core';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {urls} from '../constants/urls';

export interface UserSearchComplex {
    authHistoryId: number;
    creationDate: string;
    employeeId: number;
    firstName: string;
    ip: string;
    lastName: string;
    location: string;
    meta: string;
    title: string;
    type: string;
    userId: number;
    userName: string;
    userType: string;
}

export interface UserSearchNormal {
    employeeNumber: number;
    firstName: string;
    isActive: number;
    language: string;
    lastName: string;
    roles: Array<any>;
    title: string;
    userAccessGroups: Array<any>;
    userId: number;
    userName: string;
}

/**
 *  providedIn: 'root', }) export class UserService { } 'root' means that we want provide the service at the root level (AppModule)
 *  When you provide the service at the root level, Angular creates a single, shared instance of service and injects into any class
 *  that asks for it.
 */
@Injectable({
    providedIn: 'root'
})
export class UserService {
    userSearchComplexUrl = '';
    userSearchNormalUrl = '';

    constructor(private http: HttpClient) {
    }

    getComplexSearchedUsers() {
        return this.http.get(this.userSearchComplexUrl) as Observable<UserSearchComplex[]>;
    }

    getNormalSearchedUsers(options?: {}): Observable<HttpResponse<Object>> {
        const url = `${urls.user.usersPath}`;
        return this.http.get<HttpResponse<Object>>(url, options);
    }

    getUserById(userId, options: {}): Observable<HttpResponse<Object>> {
        const url = `${urls.user.usersPath}${userId}`;
        return this.http.get<HttpResponse<Object>>(url, options);
    }

    setFavoriteApp(paramObj, userId) {
        const url = `${urls.user.users}` + userId;
        return this.http.post(url, paramObj);
    }

    changeStatus(user, options = {}): Observable<HttpResponse<Object>> {
        let url = `${urls.user.usersPath}${user.userId}/`;
        url += user.isActive === 1 ? 'reActivate' : 'suspend';
        return this.http.post<HttpResponse<Object>>(url, options);
    }

    getRoles(options: {}): Observable<HttpResponse<Object>> {
        return this.http.get<HttpResponse<Object>>(urls.user.roles, options);
    }

    createUpdateUser(options = {userId: 0}): Observable<HttpResponse<Object>> {

        if (options.userId) {
            const url = `${urls.user.usersPath}${options.userId}`;
            return this.http.put<HttpResponse<Object>>(url, options);
        } else {
            const url = `${urls.user.usersPath}`;
            return this.http.post<HttpResponse<Object>>(url, options);
        }

    }

    getAssignedRolesAppList(options: {}): Observable<HttpResponse<Object>> {
        return this.http.get<HttpResponse<Object>>(urls.user.applications, options);
    }

}
