import {protocol, webServerDNS} from './url.common.constants';

export interface UrlAlertConstantsInterface {
    socketAlert: string;
    alertsPath: string;
    alertCountsPath: string;
    alertConfigurationPath: string;
    metricsPath: string;
    filter: string;
    tableAlerts: string;
    alertCountsByCategoryPath: string;
}

const basePath = '/api/alert';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;

export const alert: UrlAlertConstantsInterface = {
    socketAlert: 'wss://' + webServerDNS + '/alert/websocket',
    alertsPath: baseUrl + 'alerts',
    alertCountsPath: baseUrl + 'alertCounts',
    tableAlerts: baseUrl + 'alertCounts/tables',
    alertConfigurationPath: baseUrl + 'alertConfiguration',
    metricsPath: baseUrl + 'metrics',
    filter: baseUrl + 'filters',
    alertCountsByCategoryPath: baseUrl + 'alertCountsByCategory'
};

