import {protocol, webServerDNS} from './url.common.constants';

export interface UrlPlayersInterface {
    playersSearch: string;
}

const basePath = '/api/player';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;

export const player: UrlPlayersInterface = {
    playersSearch: baseUrl + 'players/'
};
