import {protocol, webServerDNS} from './url.common.constants';

export interface UrlUserConstantsInterface {
    usersPath: String;
    users: String;
    roles: string;
    applications: string;
}

const basePath = '/api/user';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;

export const user: UrlUserConstantsInterface = {
    usersPath: baseUrl + 'users/',
    users: baseUrl + 'users/',
    roles: baseUrl + 'roles/',
    applications: baseUrl + 'applications/',
};

