import {Component, Input, OnInit} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import {AppMenuComponent} from './app-menu.component';
import {dialogSize} from '../constants/constants';
import {UtilityService} from '../services/utility.service';

@Component({
    selector: 'app-switcher',
    templateUrl: './app-switcher.component.html',
    styleUrls: ['./app-switcher.component.scss']
})
export class AppSwitcherComponent implements OnInit {
    isLoginApp: boolean;
    isPPMaster: boolean;
    @Input('currentAppCode') currentAppCode;

    constructor(private snackBar: MatSnackBar, public dialog: MatDialog, public utilService: UtilityService) {
        this.utilService.closeDialog.subscribe((res) => {
            this.closeAppMenu();
        }, (err) => {
            console.log(err);
        });
    }

    ngOnInit() {
        const authValues = JSON.parse(localStorage.getItem('authValues'));
        this.isLoginApp = authValues.clientId === 'lgn';
        location.href.includes('ppmaster') ? this.isPPMaster = true : this.isPPMaster = false;
    }

    openAppMenu(): void {
        this.dialog.open(AppMenuComponent, {
            width: dialogSize.medium,
            panelClass: 'app-switcher__wrapper',
            data: {currentAppCode: this.currentAppCode}
        });
    }
    closeAppMenu(): void {
        this.dialog.closeAll();
    }
}


