import {protocol, webServerDNS} from './url.common.constants';

export interface UrlGameInterface {
    paginatedSearchManualRatings: string;
    paginatedSessions: string;
    paginatedGames: string;
    bets: string;
    sessions: string;
    sessionsByIds: string;
    updateIrcNumber: string;
    sessionNote: string;
    manualRatings: string;
}

const basePath = '/api/game';
const version = '/v1/';
const baseUrl = protocol + webServerDNS + basePath + version;

export const game: UrlGameInterface = {
    paginatedSearchManualRatings: baseUrl + 'paginatedSearchManualRatings',
    paginatedSessions: baseUrl + 'paginatedSessions',
    paginatedGames: baseUrl + 'paginatedGames',
    bets: baseUrl + 'bets',
    sessions: baseUrl + 'sessions',
    sessionsByIds: baseUrl + 'sessionsByIds',
    updateIrcNumber: baseUrl + 'updateIrcNumber',
    sessionNote: baseUrl + 'sessionNote',
    manualRatings: baseUrl + 'manualRatings'
};

